import React from 'react'
import styled from 'styled-components'
import { isNil, length, keys, pathOr, gt, __, map, filter } from 'ramda'
import cx from 'classnames'
import cheerio from 'cheerio'
import { Row, Col, Divider as AntDivider, Form } from 'antd'
import { useBreadcrumb } from '@/hooks/useApp'
import { useAuth } from '@/hooks/useAuth'
import { createFormItemLayout } from '@/utils/form'
import { Snippets, SnippetProps, MedicalOrder } from '@/constants/types'
import { Input } from '@/components/input'
import { mapIndexed, toThousandSeparator } from '@/utils/webHelper'
const { compose } = require('ramda')

type Props = {
  className?: string
  style?: React.CSSProperties
  data: {
    template: any
    snippetList: Snippets
    medical: MedicalOrder | null
  }
}

const formItemLayout = createFormItemLayout(8)
const shortFormItemLayout = createFormItemLayout(3)

const FormItem = styled(Form.Item)`
  margin-bottom: 12px;
`

const Divider = styled(AntDivider)`
  margin: 16px 0;
`

const SnippetPreviewWrapper = styled.div`
  .ant-card-head {
    min-height: auto;
  }

  .ant-card-head-title {
    padding-top: 0;
    font-size: 14px;
  }

  .ant-card-body {
    padding-top: 6px;
    padding-bottom: 8px;
    padding-left: 18px;
  }
`

export default function ReportTypingPreview({ className, style, data }: Props) {
  useBreadcrumb([{ name: '輸入報告' }, { name: '文件報告' }])

  const { snippetList, medical } = data

  const { user } = useAuth()

  const getHtmlText = (html: string, idx: number) => {
    const $ = cheerio.load(html)

    $('input').each((_, el) => {
      const $el = $(el)
      $el.replaceWith(toThousandSeparator($el.val()) + ' ')
    })

    $('select').each((_, el) => {
      const $el = $(el)
      $el.replaceWith(` ${toThousandSeparator($el.val())} `)
    })

    const div = document.createElement('div')
    div.innerHTML = $.html()

    const text = div.textContent || div.innerText || ''
    div.remove()

    return `${idx}. ${text}`
  }

  return (
    <div style={style} className={cx('js-export-pdf', className)}>
      <Row className="mb-3">
        <Col span={24}>
          <div className="text-xl text-center mt-2">
            高雄榮總
            <span className="mx-6">
              {user.division === 'RAD' ? '放射線部' : '病理檢驗部'}
            </span>
            檢查報告單
          </div>
        </Col>
      </Row>
      <Divider />
      <Row gutter={16}>
        <Col span={8}>
          <FormItem {...formItemLayout} label="病房床號">
            <Input readOnly bordered={false} value={medical?.hbed} />
          </FormItem>
        </Col>
        <Col span={8}>
          <FormItem {...formItemLayout} label="病患姓名">
            <Input readOnly bordered={false} value={medical?.hnamec} />
          </FormItem>
        </Col>
        <Col span={8}>
          <FormItem {...formItemLayout} label="病歷號">
            <Input readOnly bordered={false} value={medical?.hhisnum} />
          </FormItem>
        </Col>
      </Row>
      <Row gutter={16}>
        <Col span={8}>
          <FormItem {...formItemLayout} label="病患性別">
            <Input readOnly bordered={false} value={medical?.hsex} />
          </FormItem>
        </Col>
        <Col span={8}>
          <FormItem {...formItemLayout} label="病患身分">
            <Input readOnly bordered={false} value={medical?.cfinancl} />
          </FormItem>
        </Col>
        <Col span={8}>
          <FormItem {...formItemLayout} label="出生日期">
            <Input readOnly bordered={false} value={medical?.hbirthdt} />
          </FormItem>
        </Col>
      </Row>
      <Row gutter={16}>
        <Col span={8}>
          <FormItem {...formItemLayout} label="申請序號">
            <Input readOnly bordered={false} value={medical?.orreqno} />
          </FormItem>
        </Col>
        <Col span={8}>
          <FormItem {...shortFormItemLayout} label="開立科別">
            <Input readOnly bordered={false} value={medical?.csect} />
          </FormItem>
        </Col>
        <Col span={8}>
          {user.division === 'RAD' ? (
            <FormItem {...formItemLayout} label="檢查日期">
              <Input readOnly bordered={false} value={'xxxx'} />
            </FormItem>
          ) : (
            <FormItem {...formItemLayout} label="病理號">
              <Input readOnly bordered={false} value={medical?.orworkno} />
            </FormItem>
          )}
        </Col>
      </Row>
      <Row>
        <Col span={24}>
          <FormItem {...shortFormItemLayout} label="醫囑項目">
            <div
              dangerouslySetInnerHTML={{ __html: `${medical?.orword}` }}
              style={{
                maxWidth: '100%',
                whiteSpace: 'initial',
                borderBottom: '1px solid #d9d9d9',
                paddingBottom: 5,
                paddingLeft: 5,
                marginLeft: -15,
              }}
            />
          </FormItem>
        </Col>
      </Row>
      {user.division === 'RAD' && (
        <Row>
          <Col span={24}>
            <FormItem {...shortFormItemLayout} label="檢查日期">
              <Input readOnly bordered={false} value={'xxxx'} />
            </FormItem>
          </Col>
        </Row>
      )}
      <Divider />
      <Row>
        <Col span={24}>
          <SnippetPreviewWrapper>
            {compose(
              map((key: string) => (
                <div
                  key={key}
                  css={{
                    fontSize: 15,
                    lineHeight: 1.65,
                    color: 'rgba(0, 0, 0, 0.85)',
                  }}>
                  {compose(
                    gt(__, 0),
                    length,
                    pathOr([], [key])
                  )(snippetList) && (
                    <h2
                      className="my-1 font-normal underline"
                      css={{ fontSize: 16 }}>
                      {key}
                    </h2>
                  )}
                  {compose(
                    mapIndexed(
                      ({ setting, ...x }: SnippetProps, idx: number) => (
                        <div key={x.id}>
                          {(() => {
                            if (x.inline) {
                              return (
                                <div
                                  dangerouslySetInnerHTML={{
                                    __html: getHtmlText(x.html, idx + 1),
                                  }}
                                />
                              )
                            }

                            return (
                              <div>
                                {idx + 1}. {setting.text}: {setting.value}
                              </div>
                            )
                          })()}
                        </div>
                      )
                    ),
                    filter(
                      ({ setting, ...x }: SnippetProps) =>
                        (!isNil(setting) && !isNil(setting.value)) ||
                        x.type === 'TemplateSectionHeader' ||
                        x.inline
                    ),
                    pathOr([], [key])
                  )(snippetList)}
                </div>
              )),
              keys
            )(snippetList)}
          </SnippetPreviewWrapper>
        </Col>
      </Row>
      <Divider />
      <Row gutter={16}>
        <Col span={8}>
          <FormItem {...formItemLayout} label="判讀醫師">
            <Input bordered={false} />
          </FormItem>
        </Col>
        <Col span={8}>
          <FormItem {...formItemLayout} label="複審醫師">
            <Input bordered={false} />
          </FormItem>
        </Col>
        <Col span={8}>
          <FormItem {...formItemLayout} label="申請醫師">
            <Input bordered={false} value={medical?.ordocnm} />
          </FormItem>
        </Col>
      </Row>
      <Row gutter={16}>
        <Col span={12}>
          <FormItem {...formItemLayout} label="報告輸入人員">
            <Input bordered={false} />
          </FormItem>
        </Col>
        <Col span={12}>
          <FormItem {...formItemLayout} label="報告複審人員">
            <Input bordered={false} />
          </FormItem>
        </Col>
      </Row>
      <Divider />
      <Row className="mb-4">
        <Col span={24}>
          <div className="text-center">病解專醫字第000001號</div>
        </Col>
      </Row>
    </div>
  )
}
