import React from 'react'
import { includes, pathOr } from 'ramda'
import { XYCoord } from 'react-dnd'
import { isNil, mapIndexed } from '@/utils/webHelper'
import Snippet from '@/components/snippet'
import { SnippetProps } from '@/constants/types'
const { compose } = require('ramda')

/* prettier-ignore */
const REPORT_TEMPLATE_BUILDER_CLASSNAME = 'report-template-builder__main-content'
const UI_DRAGGABLE_CLASSNAME = 'ui-draggable'

const hasClassName = (nodes: Element[], className: string) => {
  return nodes.find(
    n => !isNil(n.className) && includes(className)(n.className)
  )
}

export function canMoveSnippet(
  element: Element | null,
  className = REPORT_TEMPLATE_BUILDER_CLASSNAME
) {
  let elem = element

  const nodes: Element[] = []
  nodes.push(elem as Element)

  while (elem?.parentNode) {
    nodes.unshift(elem.parentNode as Element)
    elem = elem.parentNode as Element
  }

  if (
    hasClassName(nodes, 'report-template-builder-contentarea--fixed-layout') ||
    hasClassName(nodes, 'snippet-drawer-menu')
  ) {
    return false
  }
  if (
    hasClassName(nodes, className) ||
    hasClassName(nodes, UI_DRAGGABLE_CLASSNAME)
  ) {
    return true
  }

  return false
}

function findSnippet(
  element: HTMLElement,
  className = UI_DRAGGABLE_CLASSNAME
): HTMLElement | null {
  if (element.classList.contains(className)) {
    return element
  }
  return element.parentElement && findSnippet(element.parentElement, className)
}

export function getPlaceHolderIndex(clientOffset: XYCoord) {
  const hoverElement = document.elementFromPoint(clientOffset.x, clientOffset.y)

  // find ui-draggable classname
  const overSnippet = findSnippet(hoverElement as NonNullable<HTMLElement>)

  const snippetIndex = overSnippet && overSnippet.dataset.index
  const placeholderIndex = overSnippet ? Number(overSnippet.dataset.index) : -1

  const isOverSnippet = !isNil(snippetIndex)

  return {
    overSnippet,
    placeholderIndex,
    isOverSnippet: isOverSnippet,
  }
}

export function renderSnippets(props: { [key: string]: any } = {}) {
  return (snippetList?: SnippetProps[]) =>
    compose(
      mapIndexed((item: SnippetProps, idx: number) => (
        <Snippet
          {...props}
          key={item.id}
          wrapperId={`${item.id}`}
          item={item}
          draggable={false}
          tabSize={item.tabSize}>
          {item.inline ? (
            <div
              id={`${item.id}`}
              className={`${item.id}`}
              css={{ padding: 16 }}
              dangerouslySetInnerHTML={{ __html: item.html }}
            />
          ) : (
            <item.Component
              id={item.id}
              className={item.id}
              item={item}
              index={idx}
            />
          )}
        </Snippet>
      )),
      pathOr([], [])
    )(snippetList)
}
