import React from 'react'
import { ModalProps, ModalFuncProps } from 'antd/lib/modal'
import { find, path } from 'ramda'
import Modal from '@/components/modal'
import { modalDefaultProps } from '@/utils/message'

export const confirm = Modal.confirm

type Props<T> = {
  /**
   * 是否可刪除: false的話不會跳確認刪除窗
   */
  isDeletable?: boolean
  /**
   * 刪除確認視窗的文字內容
   */
  content?: string | React.ReactNode
  /**
   * 確認刪除的行為，例如打 API
   */
  onRequestDelete?: (values?: any, resolve?: (values?: any) => any) => any
  /**
   * 跳出確認刪除時可以做的事情，例如關掉正在編輯的對話框
   */
  onBeforeDelete?: () => any
  /**
   * 刪除成功後，可重新取得(列表)資料
   */
  refetchQueries?: () => any
  /**
   * 取消刪除後做的事情，例如重新打開編輯對話窗
   */
  onCancel?: () => any
  /**
   * 列表資料，可在跳確認窗時從裡面查找要刪除的資料並回傳
   */
  dataSource?: T[]
  /**
   * 刪除的項目，可以是單一物件或陣列物件...
   */
  deleteItem?: any
  /**
   * 自訂確認視窗屬性
   */
  modalProps?: ModalProps
  /**
   * 刪除成功後可作的事情
   */
  onAfterDelete?: () => any
}

export let modal: {
  destroy: () => void
  update: (newConfig: ModalFuncProps) => void
}

export function useDelete<T extends any>(props: Props<T>) {
  const {
    isDeletable = true,
    content,
    dataSource = [],
    onRequestDelete,
    onBeforeDelete,
    refetchQueries,
    onCancel,
    deleteItem,
    modalProps = {},
    onAfterDelete,
  } = props

  let onAfterDeleteResolve = React.useRef(() => ({}))

  const handleConfirmDeletion = (evt: React.MouseEvent<HTMLElement>) => {
    if (onBeforeDelete) {
      onBeforeDelete()
    }
    handleConfirm(evt)
  }

  const handleDeleteFailure = () => {
    if (modal) {
      modal.update({
        okButtonProps: {
          className: 'c-btn c-btn--danger',
          style: { minWidth: 96, marginRight: 104 },
          loading: false,
        },
      })
    }
  }

  const handleConfirm = (evt: React.MouseEvent<HTMLElement>) => {
    if (!isDeletable) {
      return
    }

    const item = find<any>(
      x =>
        `${path(['id'], x)}` ===
        (evt.currentTarget as HTMLElement).dataset['id']
    )(dataSource)

    modal = confirm({
      ...modalDefaultProps(content, modalProps),
      onOk() {
        if (modal) {
          modal.update({
            okButtonProps: {
              className: 'c-btn c-btn--danger',
              style: { minWidth: 80, marginRight: 24 },
              loading: true,
            },
          })
        }

        return new Promise(resolve => {
          if (onRequestDelete) {
            onRequestDelete(item || deleteItem)
          }

          if (onAfterDelete) {
            onAfterDelete()
          }

          onAfterDeleteResolve.current = (() => {
            resolve()
            if (refetchQueries) {
              refetchQueries()
            }
          }) as any
        })
      },
      onCancel() {
        if (onCancel) {
          onCancel()
        }
      },
    })
  }

  return {
    handleConfirmDeletion,
    handleConfirm,
    handleDeleteFailure,
    onAfterDelete: onAfterDeleteResolve,
  }
}

export default useDelete
