import React from 'react'
import {
  length,
  head,
  of,
  pathOr,
  map,
  ifElse,
  always,
  isNil,
  is,
  identity,
  invoker,
  equals,
  __,
  anyPass,
} from 'ramda'
import {
  SnippetProps,
  ReportTemplateQueryResult,
  Mutation,
  MutationOptions,
  PhraseTemplate,
  QueryVariables,
} from '@/constants/types'
import useAuth from '@/hooks/useAuth'
import { reportTemplateStatusEnums } from '@/constants/enums'
import { setComponent } from '@/components/snippet-template-list'
import useSWR from '@/hooks/useSWR'
import { useRequest } from '@/utils/request'
import { toMoment, toDateString } from '@/utils/webHelper'
const { compose } = require('ramda')

const convertData = compose(
  setComponent,
  map((x: ReportTemplateQueryResult) => ({
    ...x,
    id: x.no,
    name: x.templateName,
    description: x.templateDescription,
    setting: compose(
      map((s: SnippetProps) => ({
        ...s,
        ...(s.inline ? { html: decodeURIComponent(s.html) } : {}),
      })),
      ifElse(
        isNil,
        always([]),
        compose(
          ifElse(is(Object), identity, always([])),
          ifElse(is(String), val => invoker(1, 'parse')(val)(JSON), identity),
          ifElse(
            anyPass([equals(__, 'undefined'), equals(__, '[object Object]')]),
            always([]),
            identity
          )
        )
      )
    )(x.templateJson),
    statusName: '草稿',
    createdAt: toMoment(x.createDate),
    createdAtStr: toDateString(x.createDate, 'YYYY.MM.DD HH:mm'),
    updatedAt: toMoment(x.updateDate),
    updatedAtStr: toDateString(x.updateDate, 'YYYY.MM.DD HH:mm'),
  })),
  pathOr([], [])
)

// 報告模版清單
function useUserPhraseTemplates({
  id = 0,
  division = 'PATH',
  ...params
}: QueryVariables = {}) {
  const { data, ...rest } = useSWR<ReportTemplateQueryResult[]>(
    `/api/v1/c5/getPhraseTemplateList/{id}&{division}`,
    {
      variables: { ...params, id, division },
    }
  )

  return {
    ...rest,
    data: convertData(data) as PhraseTemplate[],
  }
}

function useAllPhraseTemplates({
  id = 0,
  division = 'PATH',
  ...params
}: QueryVariables = {}) {
  const { data, ...rest } = useSWR<ReportTemplateQueryResult[]>(
    `/api/v1/c5/getCompletePhraseTemplateList/{id}&{division}`,
    {
      variables: { ...params, id, division },
    }
  )

  return {
    ...rest,
    data: convertData(data) as PhraseTemplate[],
  }
}

// 片語模版資料
function usePhraseTemplate({
  userId = 0,
  division = 'PATH',
  name = '',
  data,
  ...params
}: {
  [key: string]: any
  userId?: React.Key
  division?: string
  name?: string
  data?: any
}) {
  const response = useSWR<ReportTemplateQueryResult[]>(
    name ? `/api/v1/c5/getPhraseTemplate/{userId}&{division}&{name}` : null,
    {
      variables: {
        ...params,
        userId,
        division,
        name,
      },
    }
  )

  console.log(response)

  const [phraseTemplate, setPhraseTemplate] = React.useState<PhraseTemplate>({
    setting: [],
  } as any)

  React.useEffect(() => {
    if (data?.id) {
      setPhraseTemplate(compose(head, setComponent, of)(data))
    }
  }, [data])

  // React.useEffect(() => {
  //   if (id) {
  //     setPhraseTemplate(
  //       compose(find((x: PhraseTemplate) => `${x.id}` === `${id}`))(
  //         setComponent(phraseTemplateList)
  //       )
  //     )
  //   }
  // }, [id]) // eslint-disable-line

  let _snippetList = pathOr([], ['setting'])(phraseTemplate)

  React.useEffect(() => {
    setSnippetList(_snippetList)
  }, [JSON.stringify(_snippetList)]) // eslint-disable-line

  const [snippetList, setSnippetList] = React.useState<SnippetProps[]>(
    _snippetList
  )

  let snippetById: { [key: string]: SnippetProps } = {}

  return {
    ...response,
    phraseTemplate,
    snippetList,
    setSnippetList,
    snippetById: map((x: SnippetProps) => (snippetById[x.id] = x), snippetList),
  }
}

function useCreateOrUpdatePhraseTemplateAPI(options: MutationOptions = {}) {
  const { loading, mutate } = useRequest(`/api/v1/c5/createPhraseTemplate`, {
    ...options,
    method: 'POST',
  })

  return [loading, mutate] as Mutation
}

function useDeletePhraseTemplateAPI(options: MutationOptions = {}) {
  const { loading, mutate } = useRequest(`/api/v1/c4/delectPhraseTemplate`, {
    ...options,
    method: 'POST',
  })

  return [loading, mutate] as Mutation
}

export const usePhraseList = (variables: QueryVariables = {}) => {
  const { user } = useAuth()
  const { data, ...rest } = useUserPhraseTemplates({ ...user, ...variables })

  const dataSource = {
    content: data,
    total: length(data),
    totalPages: Math.floor(length(data) / 10 + 1),
  }

  return {
    ...rest,
    dataSource,
    data: dataSource,
  }
}

export const useAllPhraseList = (variables: QueryVariables = {}) => {
  const { user } = useAuth()
  const { data, ...rest } = useAllPhraseTemplates({ ...user, ...variables })

  const dataSource = {
    content: data,
    total: length(data),
    totalPages: Math.floor(length(data) / 10 + 1),
  }

  return {
    ...rest,
    dataSource,
    data: dataSource,
  }
}

export const useCreateOrUpdatePhrase = (options: MutationOptions = {}) => {
  const [loading, mutate] = useCreateOrUpdatePhraseTemplateAPI(options)
  const { user } = useAuth()

  const handler = (values: PhraseTemplate) => {
    return mutate({
      ...values,
      userId: user.id,
      division: user.division,
      status: reportTemplateStatusEnums.DRAFT,
      templateName: values.name,
      templateDescription: values.description,
      templateJson: JSON.stringify(values.setting),
    })
  }

  return [loading, handler] as Mutation
}

export const useRemovePhrase = (options: MutationOptions = {}) => {
  const [loading, mutate] = useDeletePhraseTemplateAPI(options)
  const { user } = useAuth()

  const hanlder = (values: PhraseTemplate) => {
    return mutate({
      ...values,
      userId: user.id,
      division: user.division,
      TemplateNAME: values.name,
    })
  }

  return [loading, hanlder] as Mutation
}

export const usePhraseItem = (variables: { id?: string; data?: any }) => {
  const { user } = useAuth()
  const { setSnippetList, snippetList, ...rest } = usePhraseTemplate({
    ...variables,
    userId: user.id,
    division: user.division,
    name: variables.id,
  })

  return {
    ...rest,
    snippetList,
    setSnippetList,
    handleSnippetUpdate: setSnippetList,
  }
}
