import React from 'react'
import { ColumnProps, TableProps, TablePaginationConfig } from 'antd/lib/table'
import { SpinProps } from 'antd/lib/spin'
import { mergeDeepRight } from 'ramda'
import { DataSource } from '@/constants/types'
import Table from '@/components/table'

type Props<T> = Omit<TableProps<T>, 'dataSource'> & {
  columns: ColumnProps<T>[]
  className?: string
  pageNum?: number
  dataSource: DataSource<T>
  onChange?: (pagination: TablePaginationConfig) => any
  loading?: boolean | SpinProps
  rowSelection?: {
    selectedRowKeys: any[]
    onChange: (selectedRowKeys: any, selectedRows: any) => void
  }
}

export default function QueryTable<T>({
  columns,
  className,
  pageNum = 1,
  dataSource,
  onChange,
  loading = false,
  rowSelection,
  pagination = {},
  ...props
}: Props<T>) {
  const [_pageNum, setPageNum] = React.useState(pageNum)

  const handleTableChange = ({ current, pageSize }: TablePaginationConfig) => {
    if (onChange) {
      onChange({ current, pageSize })
    }
    if (current) {
      setPageNum(current)
    }
  }

  let paginationConfig: false | TablePaginationConfig = mergeDeepRight<
    any,
    any
  >(
    {
      current: _pageNum,
      pageSize: 10,
      total: dataSource.total,
      showSizeChanger: false,
    },
    pagination
  )

  if (pagination === false) {
    paginationConfig = false
  }

  return (
    <Table
      rowKey="id"
      dataSource={dataSource.content}
      columns={columns as any}
      pagination={paginationConfig}
      onChange={handleTableChange}
      rowSelection={rowSelection}
      loading={loading}
      className={className}
      {...props}
    />
  )
}
