import React from 'react'
import Cookies from 'js-cookie'
import { Switch, Route } from 'react-router-dom'
import { Layout } from 'antd'
import ErrorBoundary from '@/components/error-boundary'
import Exception from '@/components/exception'
import Loading from '@/components/loading'
import AppHeader from '@/components/app-header'
import { useRoutes, RouteItem } from '@/constants/routes'
import { User } from '@/constants/types'
import Home from '@/containers/home'
import Login from '@/containers/login'
import useAuth from '@/hooks/useAuth'
import { queryString } from '@/utils/webHelper'
import history from '@/utils/history'

function App() {
  const { isAuthenticated, setUser, setIsAuthenticated } = useAuth()

  const routes = useRoutes()

  const renderRouteItem = React.useCallback((item: RouteItem) => {
    const path = `${item.path}${item.param ? `/${item.param}` : ''}`

    return (
      <Route
        exact
        key={item.id}
        path={path}
        component={
          item.component ? item.component : () => <h1> {item.title} </h1>
        }
      />
    )
  }, [])

  const renderRoute: any = React.useCallback(
    (data: RouteItem[]) => {
      return data.map((item: RouteItem) => {
        return [
          renderRouteItem(item),
          item.children && renderRoute(item.children),
          item.hide && renderRoute(item.hide),
        ]
      })
    },
    [renderRouteItem]
  )

  const user = queryString(history.location.search)

  React.useEffect(() => {
    if (user.userid) {
      const loginUser = {
        ...user,
        id: user.userid,
        name: user.user,
        isAdmin: false,
        viewDivision: false,
      } as User

      setIsAuthenticated(true)
      setUser(loginUser)

      Cookies.set('vghks_user', loginUser)
      history.push('/')
    }
  }, [JSON.stringify(user)]) // eslint-disable-line

  return (
    <ErrorBoundary>
      {!isAuthenticated && <Login />}
      {isAuthenticated && (
        <Layout>
          <AppHeader />
          <React.Suspense fallback={<Loading />}>
            <Switch>
              <Route exact path="/" component={Home} />
              {renderRoute(routes)}
              <Route component={Exception} />
            </Switch>
          </React.Suspense>
        </Layout>
      )}
    </ErrorBoundary>
  )
}

export default App
