import React from 'react'
import {
  length,
  pathOr,
  ifElse,
  isNil,
  identity,
  takeLast,
  compose,
  filter,
  concat,
} from 'ramda'
import { ModalFuncProps } from 'antd/lib/modal'
import useDelete from './useDelete'

type Props<T> = {
  defaultRowKeys?: number[] | string[]
  defaultRows?: T[]
  multiple?: boolean
  limit?: number
  modalProps?: ModalFuncProps
  onRequestDelete?: (values: any) => any
  refetchQueries?: () => any
}

export default function useRowSelection<T = any>(props: Props<T> = {}) {
  const {
    limit,
    defaultRowKeys = [],
    defaultRows = [],
    multiple = true,
  } = props

  const [selectedRowKeys, setSelectedRowKeys] = React.useState<
    string[] | number[]
  >(defaultRowKeys)

  const [selectedRows, setSelectedRows] = React.useState<T[]>(defaultRows)

  const onSelectChange = (rowKeys: React.Key[], rows: T[]) => {
    if (!isNil(limit) && length(rowKeys) > limit) {
      return
    }

    setSelectedRowKeys(ifElse(() => multiple, identity, takeLast(1))(rowKeys))

    setSelectedRows(prev =>
      ifElse(
        () => multiple,
        compose(
          filter((x: any) => rowKeys.includes(x.id)),
          concat(prev)
        ),
        takeLast(1)
      )(rows)
    )
  }

  const isDeletable = React.useMemo(() => selectedRowKeys.length > 0, [
    selectedRowKeys,
  ])

  const rowSelection = {
    selectedRowKeys,
    onChange: onSelectChange,
  }

  const { handleConfirm, onAfterDelete } = useDelete({
    ...props,
    isDeletable,
    content: `確定要刪除 ${length(selectedRowKeys as any)} 筆資料嗎？`,
    deleteItem: selectedRows.map(x => ({ id: pathOr('code', ['id'], x) })),
    onAfterDelete: () => {
      setSelectedRowKeys([])
      setSelectedRows([])
    },
  })

  return {
    rowSelection,
    isDeletable,
    handleConfirm,
    selectedRowKeys,
    selectedRows,
    selectedRowCount: selectedRowKeys.length,
    onAfterDelete,
    clearSelection: () => {
      setSelectedRowKeys([])
      setSelectedRows([])
    },
  }
}
