import React from 'react'
import styled from 'styled-components'
import Button from '@/components/button'

type ErrorInfo = {
  componentStack: string
}

type FallbackProps = {
  error?: Error
  componentStack?: string
  resetErrorBoundary: () => void
}

type ErrorBoundaryProps = {
  onResetKeysChange?: (
    prevResetKeys: Array<any> | undefined,
    resetKeys: Array<any> | undefined
  ) => void
  onReset?: (...args: any) => void
  onError?: (error: Error, componentStack: string) => void
  resetKeys?: Array<any>
  FallbackComponent?: React.ComponentType<FallbackProps>
  fallbackRender?: (props: FallbackProps) => React.ReactElement<any, any> | null
  fallback?: React.ReactElement<any, any> | null
}

type ErrorBoundaryState = {
  hasError?: boolean
  error?: Error | null
  info?: ErrorInfo | null
}

const ErrorFallbackWrapper = styled.div`
  width: 100%;
  height: 100vh;
  margin: 0 auto;
  padding: 50px;
`

function ErrorFallback({
  error,
  componentStack,
  resetErrorBoundary,
}: FallbackProps) {
  return (
    <ErrorFallbackWrapper role="alert">
      <p className="text-xl">Something went wrong:</p>
      <pre className="text-sm">{error?.message}</pre>
      <pre className="text-sm">{componentStack}</pre>
      <Button onClick={resetErrorBoundary}>Try again</Button>
    </ErrorFallbackWrapper>
  )
}

const changedArray = (a: any[] | undefined = [], b: any[] | undefined = []) =>
  a.length !== b.length || a.some((item, index) => !Object.is(item, b[index]))

const initialState = { error: null, info: null }
export default class ErrorBoundary extends React.Component<
  ErrorBoundaryProps,
  ErrorBoundaryState
> {
  state: ErrorBoundaryState = initialState

  resetErrorBoundary = (...args: any) => {
    this.props.onReset?.(...args)
    this.setState(initialState)
  }

  componentDidCatch(error: Error, info: ErrorInfo) {
    this.props.onError?.(error, info?.componentStack)
    this.setState({ error, info })
  }

  componentDidUpdate(prevProps: ErrorBoundaryProps) {
    const { error } = this.state
    const { resetKeys } = this.props
    if (error !== null && changedArray(prevProps.resetKeys, resetKeys)) {
      this.props.onResetKeysChange?.(prevProps.resetKeys, resetKeys)
      this.setState(initialState)
    }
  }

  render() {
    const { error, info } = this.state
    const {
      fallbackRender,
      FallbackComponent = ErrorFallback,
      fallback,
    } = this.props

    if (error !== null) {
      const props = {
        componentStack: info?.componentStack,
        error,
        resetErrorBoundary: this.resetErrorBoundary,
      }
      if (React.isValidElement(fallback)) {
        return fallback
      } else if (typeof fallbackRender === 'function') {
        return fallbackRender(props)
      } else if (FallbackComponent) {
        return <FallbackComponent {...props} />
      } else {
        throw new Error(
          'react-error-boundary requires either a fallback, fallbackRender, or FallbackComponent prop'
        )
      }
    }

    return this.props.children
  }
}

function withErrorBoundary<P>(
  Component: React.ComponentType<P>,
  errorBoundaryProps: ErrorBoundaryProps
) {
  function Wrapped(props: any) {
    return (
      <ErrorBoundary {...errorBoundaryProps}>
        <Component {...props} />
      </ErrorBoundary>
    )
  }

  // Format for display in DevTools
  const name = Component.displayName || Component.name || 'Unknown'
  Wrapped.displayName = `withErrorBoundary(${name})`

  return Wrapped
}

function useErrorHandler<P = Error>(givenError?: P) {
  const [error, setError] = React.useState(null)
  if (givenError) throw givenError
  if (error) throw error
  return setError
}

export { ErrorBoundary, withErrorBoundary, useErrorHandler }
