import React from 'react'
import styled from 'styled-components'
import { Row, Col } from 'antd'
import { Loading3QuartersOutlined } from '@ant-design/icons'

type PropTypes = {
  className?: string
  style?: React.CSSProperties
}

const Wrapper = styled.div`
  .ant-spin {
    &--inner {
      margin: 0 auto;
    }
  }
`

export function Loading(props: PropTypes) {
  return <Loading3QuartersOutlined {...props} />
}

export default React.memo(function Loading({
  className,
  style,
  ...props
}: PropTypes) {
  return (
    <Wrapper className="bg-greyF9">
      <Row justify="center" className={className}>
        <Col className="ant-spin--inner">
          <Loading3QuartersOutlined spin style={style} />
        </Col>
      </Row>
    </Wrapper>
  )
})
