import React from 'react'
import moment from 'moment'
import { replace } from 'ramda'
import useRouteLeaveConfirm from '@/hooks/useRouteLeaveConfirm'
import { useExportReportJson } from '@/services/report'
import Button from '@/components/button'
import env from '@/constants/env'
import useAuth from '@/hooks/useAuth'
import { REPORT_INQUIRY } from '@/constants/routes'
import { ReportTemplate } from '@/constants/types'
const html2pdf = require('html2pdf.js')

export enum menuState {
  VISIBLE_RIGHT = '1',
  VISIBLE_LEFT = '2',
}

type Props = {
  data: ReportTemplate
  isExport?: boolean
  exportOnly?: boolean
  onToggleExport: () => void
  onOk: () => void
  onExportJson?: () => void
  onExport?: () => void
}

export default function Header({
  data,
  isExport,
  exportOnly = false,
  onToggleExport,
  onOk,
}: Props) {
  const handleConfirm = useRouteLeaveConfirm(REPORT_INQUIRY)

  const { user } = useAuth()

  const [exporting, setIsExporting] = React.useState(false)

  const handleSave = () => {
    onOk()
  }

  const handleOpenImageViewer = () => {
    let url = replace(
      ':studyInstanceUIDs',
      '1.3.6.1.4.1.14519.5.2.1.3098.4963.119176436435873112231353675460'
    )(user.division === 'PATH' ? env.viewerUrl : env.radiolViewerUrl)

    // remove last `/`
    // let url = compose(join('/'), dropLast(1), split('/'))(envStore.appUrl)

    window.open(`${url}?PatientID=${data.hhisnum}`, '__blank')
  }

  const [exportingJson, handleExportJson] = useExportReportJson({
    onCompleted: () => {},
  })

  const _handleExportJson = () => {
    handleExportJson({
      orreqno: data.orreqno,
      division: user.division,
      filename: data.name,
    })
  }

  const handleExport = async () => {
    setIsExporting(true)

    const element = document.querySelector('.js-export-pdf')
    const opt = {
      margin: 1,
      filename: `報告匯出_${moment().format('YYYYMMDDHHmmss')}.pdf`,
      image: { type: 'jpeg', quality: 0.98 },
      html2canvas: { scale: 2 },
      jsPDF: { unit: 'in', format: 'letter', orientation: 'portrait' },
    }

    // New Promise-based usage:
    html2pdf().set(opt).from(element).save()
  }

  return (
    <div className="inline-flex">
      {!isExport && (
        <>
          <Button
            color="outline"
            className="ml-2 text-white border-white bg-transparent"
            onClick={handleSave}
            style={{ minWidth: 80 }}>
            儲存
          </Button>
          <Button
            color="outline"
            className="ml-2 text-white border-white bg-transparent"
            onClick={handleConfirm}
            style={{ minWidth: 80 }}>
            取消
          </Button>
          <Button
            color="outline"
            className="ml-2 text-white border-white bg-transparent"
            onClick={handleOpenImageViewer}
            style={{ minWidth: 80 }}>
            影像
          </Button>
          <Button
            color="outline"
            className="ml-6 text-white border-white bg-transparent"
            onClick={onToggleExport}>
            輸出報告
          </Button>
        </>
      )}
      {isExport && (
        <>
          {(user.division !== 'CLINICIAN' || exportOnly) && (
            <Button
              color="outline"
              className="text-white border-white bg-transparent"
              onClick={onToggleExport}>
              取消
            </Button>
          )}
          <Button
            color="outline"
            className="ml-6 text-white border-white bg-transparent"
            loading={exportingJson}
            onClick={_handleExportJson}>
            下載 JSON
          </Button>
          <Button
            color="outline"
            className="ml-2 text-white border-white bg-transparent"
            loading={exporting}
            onClick={handleExport}>
            下載報告
          </Button>
        </>
      )}
    </div>
  )
}
