import React from 'react'
import cx from 'classnames'
import { Form, Row, Col } from 'antd'
import { ColumnProps } from 'antd/lib/table'
import { ModalProps } from 'antd/lib/modal'
import { LinkOutlined } from '@ant-design/icons'
import {
  head,
  isNil,
  is,
  of,
  identity,
  always,
  ifElse,
  update,
  pathOr,
  path,
} from 'ramda'
import { FormItem } from '@/components/form'
import Modal from '@/components/modal'
import { Input } from '@/components/input'
import Table from '@/components/query-table'
import Button from '@/components/button'
import { useRadlexes } from '@/services/radlex'
import useTableRowSelection from '@/hooks/useTableRowSelection'
import useVisible from '@/hooks/useVisible'
import { ReportTemplateSetting } from '@/constants/types'
const { compose } = require('ramda')

interface RadlexProps {
  onSelect?: (value: { radlexCode: string }) => void
  field?: string | (string | number)[]
  inline?: boolean
  item?: ReportTemplateSetting
}

export function useRadlex(onOk?: (value: { radlexCode: string }) => void) {
  return (value: { radlexCode: string }) => {
    if (onOk) {
      onOk(value)
    }
  }
}

export function Radlex({
  onSelect,
  inline = false,
  field = 'text',
  item,
}: RadlexProps) {
  const [radlexModalVisible, handleRadlexModalVisible] = useVisible()

  const handleSelect = (radlex: { id: number; code: string }) => {
    if (onSelect) {
      onSelect(
        ifElse(
          is(Array),
          ([, idx]: any[] = []) => {
            return {
              options: compose(
                update(idx, {
                  ...pathOr({}, ['options', idx])(item),
                  radlexCode: radlex.code,
                } as any),
                pathOr([], ['options'])
              )(item),
            }
          },
          always({ radlexCode: radlex?.code })
        )(field)
      )
    }
    handleRadlexModalVisible()
  }

  let radlexCode = ifElse(
    is(Array),
    ([, idx]: any[]) => {
      return path(['options', idx, 'radlexCode'], item)
    },
    () => path(['radlexCode'], item)
  )(field)

  return (
    <>
      {radlexModalVisible && (
        <RadlexListModal
          selected={radlexCode}
          onOk={handleSelect}
          onCancel={handleRadlexModalVisible}
        />
      )}
      <FormItem noStyle={inline} label="Text" className="is--flex">
        <Form.Item noStyle name={field}>
          <Input
            className={cx({
              'is--linked': !isNil(radlexCode),
              'w-11/12': inline,
            })}
          />
        </Form.Item>
        <Form.Item noStyle>
          <LinkOutlined
            className="ml-2 cursor-pointer"
            onClick={handleRadlexModalVisible}
          />
        </Form.Item>
      </FormItem>
    </>
  )
}

interface RadlexModalProps extends Omit<ModalProps, 'onOk'> {
  onOk(value: { code: string; id: number }): void
  selected?: string
}

export function RadlexListModal({
  onOk,
  onCancel,
  selected,
  ...props
}: RadlexModalProps) {
  const { dataSource, loading } = useRadlexes()

  const [keyword, setKeyword] = React.useState<string>()

  const { rowSelection, selectedRowCount, selectedRows } = useTableRowSelection(
    {
      multiple: false,
      ...(selected
        ? {
            defaultRowKeys: ifElse(is(Array), identity, of)(selected),
          }
        : {}),
    }
  )

  const columns: ColumnProps<{ code: number; value: string }>[] = [
    {
      title: 'SNOMED CT',
      dataIndex: 'code',
      render: (v, r) => `${r.value} (${r.code})`,
    },
  ]

  const handleChange = (evt: React.ChangeEvent<HTMLInputElement>) => {
    setKeyword(evt.target.value)
  }

  const handleOk = () => {
    if (selectedRowCount > 0) {
      onOk(head(selectedRows))
    }
  }

  let _dataSource = {
    ...dataSource,
    content: dataSource.content.filter(x =>
      keyword
        ? `${x.code}`.includes(keyword) || `${x.value}`.includes(keyword)
        : true
    ),
  }

  return (
    <Modal
      {...props}
      visible
      title="選擇 SNOMED CT"
      bodyStyle={{
        paddingTop: 48,
        paddingLeft: 64,
        paddingRight: 64,
      }}
      width={650}
      footer={[
        <Button key="btn-cancel" color="outline" onClick={onCancel}>
          取消
        </Button>,
        <Button
          key="btn-ok"
          color="primary"
          onClick={handleOk}
          className="ml-3">
          確認
        </Button>,
      ]}
      onCancel={onCancel}
      keyboard={false}
      maskClosable={false}>
      <Row>
        <Col span={24}>
          關鍵字:
          <Input
            className="ml-2 w-2/3"
            placeholder="輸入 SNOMED CT"
            onChange={handleChange}
          />
        </Col>
      </Row>
      <Row className="mt-6">
        <Col span={24}>
          <Table
            rowKey="code"
            size="small"
            dataSource={_dataSource}
            loading={loading}
            columns={columns}
            pagination={{ pageSize: 12 }}
            rowSelection={rowSelection}
          />
        </Col>
      </Row>
    </Modal>
  )
}
