import React from 'react'
import { Affix } from 'antd'
import { keys, pathOr } from 'ramda'
import { mapIndexed } from '@/utils/webHelper'
import { SnippetProps, Snippets } from '@/constants/types'
import { Anchor } from './components'
const { compose } = require('ramda')

const { Link } = Anchor

type Props = {
  affixed?: boolean
  onClick: (
    e: React.MouseEvent<HTMLElement>,
    link: {
      title: React.ReactNode
      href: string
    }
  ) => void
  onChange: (affixed?: boolean) => void
  snippetList: Snippets
}

export default function ReportTypingAffix({
  affixed = false,
  onChange,
  onClick,
  snippetList,
}: Props) {
  return (
    <Affix offsetTop={0} onChange={onChange}>
      <Anchor
        showInkInFixed
        affix={false}
        className="is--fixed"
        style={getAnchorStyles(affixed)}
        onClick={onClick}>
        {compose(
          mapIndexed((key: string, idx: number) => (
            <Link key={`${key}-${idx}`} href={key} title={key}>
              {compose(
                mapIndexed((x: SnippetProps) => (
                  <Link
                    key={x.id}
                    href={`${key}-${x.id}`}
                    title={pathOr(x.id, ['setting', 'text'], x)}
                  />
                )),
                pathOr([], [key])
              )(snippetList)}
            </Link>
          )),
          keys
        )(snippetList)}
      </Anchor>
    </Affix>
  )
}

function getAnchorStyles(affixed?: boolean): React.CSSProperties | undefined {
  if (!affixed) {
    return undefined
  }

  return {
    marginLeft: -16,
    marginRight: -16,
    background: '#fff',
    padding: 24,
    borderBottomLeftRadius: 8,
    borderBottomRightRadius: 8,
  }
}
