import React from 'react'
import { RouteComponentProps } from 'react-router-dom'
import { Row, Col, BackTop, Affix, Form, message, Tooltip, Spin } from 'antd'
import {
  BulbOutlined,
  LeftOutlined,
  RightOutlined,
  DoubleLeftOutlined,
} from '@ant-design/icons'
import cx from 'classnames'
import moment from 'moment'
import {
  map,
  pathOr,
  keys,
  gt,
  __,
  length,
  ifElse,
  equals,
  always,
  filter,
  values,
  not,
  replace,
  split,
  join,
  toPairs,
  head,
  update,
  is,
  T,
  identity,
} from 'ramda'
import useTitle from '@/hooks/useTitle'
import useVisible from '@/hooks/useVisible'
import { useBreadcrumb, useHeaderContent } from '@/hooks/useApp'
import useAuth from '@/hooks/useAuth'
import { useSnippetCategoryOfReport } from '@/services/report-template'
import { isNil, queryString, uuid } from '@/utils/webHelper'
import { SnippetProps } from '@/constants/types'
import { Page } from '@/components/layout'
import ReactDndProvider from '@/components/snippet-scrollable-container'
import SnippetsContainer from '@/components/snippets-container'
import Input from '@/components/input'
import Collapse from '@/components/collapse'
import SnippetListMenu from '@/components/snippet-list-menu'
import { renderSnippets } from '@/components/snippet-utils'
import Tabs from '@/components/draggable-tabs'
import {
  useToggleReportPreview,
  ReportTemplateSelect,
  useReportTemplate,
  useAllReportTemplates,
} from '@/services/report-template'
import {
  useReportItem,
  useMedicalOrder,
  useCreateOrUpdateReport,
} from '@/services/report'
import history from '@/utils/history'
import { REPORT_EDIT } from '@/constants/routes'
import Pie from './pie'
import Header from './report-typing-header'
import ReportMedicalOrder from './report-typing-medical-order'
import ReportAffix from './report-typing-affix'
import ReportPreview from './report-typing-preview'
import PhraseListModal from './phrase-list-modal'
const { compose, cond } = require('ramda')

enum menuState {
  VISIBLE_RIGHT = '1',
  VISIBLE_LEFT = '2',
}

interface Props extends RouteComponentProps<{ id: string }> {
  id: any
  doctor: string
  orreqno: string
}

const { stringify } = JSON

const col = ifElse(equals(false), always(5), always(0))

export default function ReportTyping(props: Props) {
  useTitle('輸入報告')

  useBreadcrumb([{ name: '輸入報告' }, { name: '病理報告輸入' }])

  useToggleReportPreview(true)

  const { user } = useAuth()

  const snippetCategoryOfReport = useSnippetCategoryOfReport()

  React.useEffect(() => {
    if (user.division !== 'PATH' && user.division !== 'RAD') {
      handleExportable()
    }
  }, [user.division]) // eslint-disable-line

  const [form] = Form.useForm()

  const [exportable, handleExportable] = useVisible()

  const [phraseListModalVisible, handlePhraseListModalVisible] = useVisible()

  const [visibleLayouts, setVisibleLayouts] = React.useState<string[]>([
    menuState.VISIBLE_LEFT,
    menuState.VISIBLE_RIGHT,
  ])

  // 沒有 QS 代表是編輯，會有 params.id 要用 useReportItem 取得報告資料後，
  // 再餵進去 useReportTemplate 作編輯
  const { params } = props.match
  // 有 QS 代表新增，會沒有 params.id，此時的資料要用 reportTemplate
  const qs = queryString(props.location.search)
  // 新增時使用 QS 的 orreqno，編輯用 pramas.id 的
  const orreqno = params.id || qs.orreqno
  // 只有新增時有 QS 才會有
  const [templateName, setCurrentTemplateName] = React.useState(qs.templateName)

  const { data: medicalorder, loadingM } = useMedicalOrder({ id: orreqno })

  const { item, loading: loadingData } = useReportItem(orreqno)

  const {
    loading,
    total,
    category,
    reportTemplate,
    snippetList,
    handleTabEdit,
    handleTabChange,
    handleTabMove,
    handleTabNameChange,
    handleSnippetUpdate,
  } = useReportTemplate({
    id: templateName,
    data: item,
  })

  const _loading = loading || loadingM || loadingData

  // bind key shortcut
  React.useEffect(() => {
    const handleKeyPress = (evt: KeyboardEvent) => {
      if (exportable) {
        return
      }

      // ctrl + s
      if (evt.ctrlKey && evt.key === 'S') {
        handleOk()
      }
      // f9
      if (!evt.ctrlKey && evt.key === 'F9') {
        handlePhraseListModalVisible()
      }
    }

    window.addEventListener('keydown', handleKeyPress)

    return () => {
      window.removeEventListener('keydown', handleKeyPress)
    }
  }, []) // eslint-disable-line

  // reset templateName state while qs.templateName changed
  React.useEffect(() => {
    setCurrentTemplateName(qs.templateName)
  }, [qs.templateName])

  // reset form field values while report data changed
  React.useEffect(() => {
    if (reportTemplate) {
      form.setFieldsValue(reportTemplate)
    }
    if (medicalorder) {
      form.setFieldsValue({ status: medicalorder.orstatuscode })
    }
    if (reportTemplate.templateName) {
      form.setFieldsValue({ templateId: reportTemplate.templateName })
    }
  }, [stringify(reportTemplate), stringify(medicalorder)]) // eslint-disable-line

  // reset form field values while qs changed
  React.useEffect(() => {
    if (qs.doctor) {
      form.setFieldsValue({ doctor: qs.doctor })
    }
    if (qs.orreqno) {
      form.setFieldsValue({ orreqno: qs.orreqno })
    }
    if (!!qs.finish) {
      handleExportable()
    }
  }, [stringify(qs)]) // eslint-disable-line

  const handleLayoutChange = (value: string) => () => {
    setVisibleLayouts((prev: string[]) =>
      prev.includes(value) ? prev.filter(x => x !== value) : [...prev, value]
    )
  }

  const leftContentVisible = visibleLayouts.includes(menuState.VISIBLE_LEFT)
  const rightContentVisible = visibleLayouts.includes(menuState.VISIBLE_RIGHT)

  const [creating, handleCreateOrUpdate] = useCreateOrUpdateReport({
    onCompleted: response => {
      history.push(replace(':id', response?.data.orreqno)(REPORT_EDIT))
      message.success('報告已儲存成功')
    },
  })

  const { templateByName } = useAllReportTemplates()

  const handleOk = () => {
    form.validateFields().then(values => {
      console.log(values)
      let payload = {
        ...values,
        ...(params.id ? { orreqno: params.id } : {}),
        hhisnum: medicalorder?.hhisnum,
        templateId: pathOr(
          reportTemplate.templateId,
          [templateName, 'id'],
          templateByName
        ),
        setting: snippetList,
      }

      handleCreateOrUpdate(payload)
    })
  }

  useHeaderContent(
    <Header
      exportOnly={!!qs.finish}
      data={reportTemplate}
      isExport={exportable}
      onToggleExport={handleExportable}
      onOk={handleOk}
    />
  )

  const handleReportChange = (value: string) => {
    setCurrentTemplateName(value)
  }

  const [affixed, setIsAffixed] = React.useState<boolean | undefined>(false)

  const [percentage, setPercentage] = React.useState(0)

  const handleAffixChange = (affixed?: boolean) => {
    setIsAffixed(affixed)
  }

  const handleAnchorClick = (
    e: React.MouseEvent<HTMLElement>,
    link: {
      title: React.ReactNode
      href: string
    }
  ) => {
    e.preventDefault()

    const [tab, href] = compose(
      ([tab, ...rest]: string[]) => [tab, join('-')(rest)],
      split('-'),
      pathOr('', ['href'])
    )(link)

    if (category !== 'overview') {
      handleTabChange(tab)
    }

    if (!href) {
      return
    }

    window.scroll({
      behavior: 'smooth',
      left: 0,
      top: document.getElementById(href)?.offsetTop,
    })
  }

  const handlePhraseAdd = (values: SnippetProps[]) => {
    let newList = [
      ...snippetList[category],
      ...values.map(x => ({
        ...x,
        fromMenu: false,
        id: `${x.id}__${uuid()}`,
      })),
    ]

    handleSnippetUpdate(newList)
  }

  const handleValuesChange = (
    changedValues: any,
    { data }: { data: { [key: string]: any } }
  ) => {
    setPercentage(
      Math.round(
        (compose(length, filter(compose(not, isNil)), values)(data) / total) *
          100
      )
    )

    if (!changedValues) {
      return
    }

    const [id, value] = compose(
      pathOr([], []),
      head,
      toPairs,
      pathOr({}, ['data'])
    )(changedValues)

    if (!id) {
      return
    }

    const list = snippetList[category]
    const snippet = list.filter(x => x.id === id)[0]
    const index = list.indexOf(snippet)

    handleSnippetUpdate(
      update(
        index,
        {
          ...snippet,
          setting: {
            ...snippet.setting,
            value: cond([
              [is(Array), join('、')],
              [
                (val: any) => moment.isMoment(val),
                (val: any) =>
                  snippet.type === 'TemplateDate'
                    ? val.format('YYYY/MM/DD')
                    : val.format('HH:mm:ss'),
              ],
              [T, identity],
            ])(value),
          },
        },
        list
      )
    )
  }

  React.useEffect(() => {
    handleValuesChange(null, form.getFieldsValue())
  }, [total]) // eslint-disable-line

  const renderCategory = (category: string) => {
    return compose(gt(__, 0), length, pathOr([], [category]))(snippetList) ? (
      <h2 id={category} className="my-2">
        {category}
      </h2>
    ) : null
  }

  const colSpan = 14 + col(leftContentVisible) + col(rightContentVisible)

  const { doctor } = props

  const displayORWord = !!(
    (category === snippetCategoryOfReport.PATHOLOGICAL_DIAGNOSIS ||
      category === snippetCategoryOfReport.PROCEDURE_INFORMATION) &&
    medicalorder?.orword
  )

  return (
    <ReactDndProvider className="is--preview">
      {phraseListModalVisible && (
        <PhraseListModal
          onOk={handlePhraseAdd}
          onCancel={handlePhraseListModalVisible}
        />
      )}
      <BackTop />
      <Spin spinning={_loading || creating}>
        <Page>
          <Form
            form={form}
            labelAlign={exportable ? 'left' : 'right'}
            className="relative"
            onValuesChange={handleValuesChange}>
            {!leftContentVisible && (
              <Tooltip title="展開報告內容">
                <div
                  className="absolute z-10 bg-secondary text-white rounded-tr-md rounded-br-md flex items-center justify-center cursor-pointer py-2 pl-1"
                  css={{ left: -40, top: 7 }}
                  onClick={handleLayoutChange(menuState.VISIBLE_LEFT)}>
                  <RightOutlined className="text-xl" />
                </div>
              </Tooltip>
            )}
            <Row gutter={16}>
              {leftContentVisible && (
                <Col span={5}>
                  <Collapse
                    defaultActiveKey={[1, 2]}
                    expandIcon={() => null}
                    className="shadow-2xl">
                    <Collapse.Panel
                      header={
                        <div className="text-center relative">
                          報告內容
                          <Tooltip title="收合報告內容">
                            <DoubleLeftOutlined
                              className="absolute cursor-pointer"
                              style={{ top: 5, right: 0 }}
                              onClick={handleLayoutChange(
                                menuState.VISIBLE_LEFT
                              )}
                            />
                          </Tooltip>
                        </div>
                      }
                      key={1}>
                      <Row className="mb-2 text-sm text-darkGrey">
                        <Col span={24} className="mb-3">
                          <div className="inline-block" css={{ width: 65 }}>
                            判讀醫師
                          </div>
                          <Form.Item
                            noStyle
                            name="doctor"
                            initialValue={user.name}>
                            <Input className="w-2/3" bordered={false} />
                          </Form.Item>
                        </Col>
                        <Col span={24} className="my-3">
                          <div className="inline-block" css={{ width: 65 }}>
                            申請序號
                          </div>
                          <Form.Item noStyle name="orreqno">
                            <Input className="w-2/3" bordered={false} />
                          </Form.Item>
                        </Col>
                        <Col span={24} className="my-3">
                          <div className="inline-block" css={{ width: 65 }}>
                            報告格式
                          </div>
                          <Form.Item
                            noStyle
                            name="templateId"
                            initialValue={templateName}>
                            <ReportTemplateSelect
                              className="w-2/3"
                              onChange={handleReportChange}
                            />
                          </Form.Item>
                        </Col>
                        {category === 'overview' && (
                          <Col span={24} className="mt-3">
                            <ReportAffix
                              affixed={affixed}
                              onClick={handleAnchorClick}
                              onChange={handleAffixChange}
                              snippetList={snippetList}
                            />
                          </Col>
                        )}
                      </Row>
                    </Collapse.Panel>
                    <Collapse.Panel
                      header={<div className="text-center">模版工具</div>}
                      key={2}>
                      <Row>
                        <Col span={24}>
                          <SnippetListMenu />
                          <div
                            className="flex items-center mt-2 cursor-pointer"
                            onClick={handlePhraseListModalVisible}>
                            <BulbOutlined className="mr-2" />
                            Phrase
                          </div>
                        </Col>
                      </Row>
                    </Collapse.Panel>
                  </Collapse>
                </Col>
              )}

              <Col span={colSpan}>
                {!exportable && (
                  <>
                    <div className="pt-2 bg-white rounded-lg shadow-2xl">
                      <Affix offsetTop={0}>
                        <div
                          className={cx(
                            'px-4 mb-4 -mt-2',
                            'flex items-center justify-between',
                            'rounded-lg shadow-sm',
                            'text-xl text-darkGrey font-medium'
                          )}
                          css={{ background: '#fff' }}>
                          <div>
                            {user.division === 'PATH'
                              ? 'Pathological'
                              : 'Radiological'}{' '}
                            Image Report
                            <div className="text-base text-gray-600"></div>
                          </div>
                          <Pie
                            percent={percentage}
                            total={`${percentage}%`}
                            style={{ width: 70, paddingTop: 6 }}
                            height={80}
                          />
                        </div>
                      </Affix>
                    </div>
                    <div className="rounded-lg shadow-2xl">
                      <Tabs
                        type="editable-card"
                        activeKey={category}
                        onChange={handleTabChange}
                        onEdit={handleTabEdit}
                        tabBarGutter={8}
                        moveTabNode={handleTabMove}
                        className="flex-1">
                        {compose(
                          map((category: string) => (
                            <Tabs.TabPane
                              closable
                              key={category}
                              tab={
                                <span
                                  contentEditable
                                  suppressContentEditableWarning
                                  spellCheck={false}
                                  className="outline-none"
                                  css={{ whiteSpace: 'nowrap' }}
                                  onChange={handleTabNameChange(category)}>
                                  {category}
                                </span>
                              }>
                              <Col
                                span={24}
                                className="p-4 report-template-builder__main-content">
                                {displayORWord && (
                                  <div
                                    className="mb-4 px-1"
                                    dangerouslySetInnerHTML={{
                                      __html: `${medicalorder?.orword}`,
                                    }}
                                  />
                                )}
                                <SnippetsContainer
                                  // draggable={!isPreview}
                                  draggable
                                  snippetList={snippetList[category]}
                                  onSnippetUpdate={handleSnippetUpdate}
                                />
                              </Col>
                            </Tabs.TabPane>
                          )),
                          keys
                        )(snippetList)}
                        <Tabs.TabPane key="overview" tab="Overview">
                          {compose(
                            map((category: string) => (
                              <div key={category} className="px-3 bg-white">
                                {renderCategory(category)}
                                {compose(
                                  renderSnippets({ className: 'mb-0' }),
                                  pathOr([], [category])
                                )(snippetList)}
                              </div>
                            )),
                            keys
                          )(snippetList)}
                        </Tabs.TabPane>
                      </Tabs>
                    </div>
                  </>
                )}
                {exportable && (
                  <div className="rounded-log shadow-2x bg-white p-3 px-5">
                    <div id="editor" />
                    <ReportPreview
                      data={{
                        template: reportTemplate,
                        medical: medicalorder,
                        snippetList,
                      }}
                    />
                  </div>
                )}
              </Col>

              {rightContentVisible && (
                <Col span={5}>
                  <ReportMedicalOrder
                    data={medicalorder}
                    doctor={doctor}
                    onToggle={handleLayoutChange(menuState.VISIBLE_RIGHT)}
                  />
                </Col>
              )}
            </Row>
            {!rightContentVisible && (
              <Tooltip title="展開醫囑內容">
                <div
                  className="absolute z-10 bg-secondary text-white rounded-tl-md rounded-bl-md flex items-center justify-center cursor-pointer py-2 pr-1"
                  css={{ right: -49, top: 7 }}
                  onClick={handleLayoutChange(menuState.VISIBLE_RIGHT)}>
                  <LeftOutlined className="text-xl" />
                </div>
              </Tooltip>
            )}
          </Form>
        </Page>
      </Spin>
      ?
    </ReactDndProvider>
  )
}
