import React from 'react'
import cx from 'classnames'
import { FontColorsOutlined } from '@ant-design/icons'
import styled from 'styled-components'
import SnippetListMenuItem from '@/components/snippet-list-menu-item'
import snippetTemplateList from '@/components/snippet-template-list'

interface Props {
  className?: string
  style?: React.CSSProperties
}

export default class SnippetListMenu extends React.Component<Props> {
  render() {
    return (
      <SnippetListMenuWrapper
        className={cx('snippet-drawer-menu', this.props.className)}
        {...this.props}>
        <div className="snippet-drawer-menu__list">
          {snippetTemplateList.map(item => (
            <SnippetListMenuItem
              {...item}
              key={item.id}
              className="snippet-drawer-menu__item py-3">
              <div className="flex items-center">
                {item.icon ? (
                  <item.icon className="mr-2" />
                ) : (
                  <FontColorsOutlined className="mr-2" />
                )}
                {item.name}
              </div>
            </SnippetListMenuItem>
          ))}
        </div>
      </SnippetListMenuWrapper>
    )
  }
}

const SnippetListMenuWrapper = styled.div`
  .snippet-drawer-menu {
    &__list {
      height: 100%;
      overflow-y: auto;
      overflow-x: hidden;

      ::-webkit-scrollbar {
        display: none;
      }
    }
  }
`
