import React from 'react'
import { DragSource, ConnectDragSource } from 'react-dnd'
import styled from 'styled-components'
import ItemTypes from '@/constants/item-types'

interface Props {
  connectDragSource: ConnectDragSource
  children?: React.ReactNode
  className?: string
  id?: React.Key
}

const withDragSource = DragSource(
  ItemTypes.BOARD,
  {
    beginDrag(props: Props) {
      const { children, className, ...rest } = props // eslint-disable-line
      return {
        ...rest,
        fromMenu: true,
      }
    },
    endDrag(_, monitor) {
      if (!monitor.didDrop()) {
        return
      }
    },
    isDragging(props, monitor) {
      const isDragging = props.id === monitor.getItem().id

      return isDragging
    },
  },
  (connect, monitor) => ({
    connectDragSource: connect.dragSource(),
    connectDragPreview: connect.dragPreview(),
    isDragging: monitor.isDragging(),
  })
)

class SnippetListMenuItem extends React.Component<Props> {
  render() {
    const { connectDragSource, id, ...rest } = this.props

    return (
      <SnippetListMenuWrapper
        {...rest}
        ref={comp => connectDragSource(comp)}
        id={`${id}`}>
        <div className="snippet-drawer-menu__item--content">
          {this.props.children}
        </div>
      </SnippetListMenuWrapper>
    )
  }
}

const SnippetListMenuWrapper = styled.div`
  cursor: move;
`

export default withDragSource(SnippetListMenuItem)
