import React from 'react'
import { is } from 'ramda'
import _useSWR from 'swr'
import {
  keyInterface,
  fetcherFn,
  ConfigInterface,
  responseInterface,
} from 'swr/dist/types'
import { QueryVariables } from '@/constants/types'
import { request, format, toQueryString } from '@/utils/request'

interface Response<Data = any, Error = any>
  extends responseInterface<Data, Error> {
  loading: boolean
  called: boolean
  refetch(variables?: QueryVariables): Promise<any>
}

interface Config<Data = any, Error = any> extends ConfigInterface<Data, Error> {
  variables?: { [key: string]: any }
  lazy?: boolean
  onCompleted?: (data: Data) => void
  onError?: (error: Error) => void
}

export default function useSWR<Data = any, Error = any>(
  key: keyInterface,
  {
    variables,
    onCompleted,
    onError,
    lazy = false,
    ...config
  }: Config<Data, Error> = {},
  fn?: fetcherFn<Data>
): Response<Data, Error> {
  const [query, setQuery] = React.useState(variables)

  const [called, setIsCalled] = React.useState(!lazy)

  const val = React.useRef<Data>()

  const _fn = fn ? fn : (url: any) => request(url, { throwable: true })

  let _key = key
  if (is(String, key)) {
    const { fullUrl, params } = format(key as string, query)
    _key = `${fullUrl}?${toQueryString(params)}`
  }
  if (!called) {
    _key = null
  }

  const { data, ...rest } = _useSWR(_key, _fn, {
    ...config,
    dedupingInterval: 0,
    revalidateOnFocus: false,
    shouldRetryOnError: false,
    suspense: false,
    onSuccess: data => {
      if (onCompleted) {
        onCompleted(data)
      }
    },
    onError: error => {
      if (onError) {
        onError(error)
      }
    },
  })

  const refetch = (variables: QueryVariables = {}) => {
    setQuery(prev => ({ ...prev, ...variables }))
    setIsCalled(true)

    return Promise.resolve<any>({})
  }

  if (data !== undefined) {
    val.current = data
  }

  return {
    ...rest,
    loading: rest.isValidating,
    data: val.current,
    refetch,
    called,
  }
}
