import React from 'react'
import { pathOr, map } from 'ramda'
import { Form } from 'antd'
import Select from '@/components/select'
import TemplateWrapper from '@/components/snippet-template-wrapper'
import {
  SnippetTemplateSelectionModal,
  Option,
  defaultValue,
} from '@/components/snippet-template-selection-modal'
import { ReportTemplateSetting, ReportTemplateProps } from '@/constants/types'
import { useUpdateSnippet } from '@/services/report-template'
const { compose } = require('ramda')

interface Setting extends ReportTemplateSetting {
  options?: Option[]
  selectionType?: 'single' | 'multiple'
}

export default function SnippetTemplateSelect(props: ReportTemplateProps) {
  const { item, editModalVisible, handleEditModalVisible, onUpdate } = props

  const { setting, handleOk } = useUpdateSnippet<Setting>({
    item,
    onUpdate,
    defaultValue,
  })

  return (
    <>
      {editModalVisible && (
        <SnippetTemplateSelectionModal
          item={setting}
          onCancel={handleEditModalVisible}
          onOk={handleOk}
          freetextVisible={false}
          title={
            <>
              <div>Drop-down List</div>
              <div className="mt-1 text-sm text-gray-600">
                typically used for lists of greater than 6 or 7 options.
              </div>
            </>
          }
        />
      )}
      <TemplateWrapper title={setting.text || ''}>
        <Form.Item noStyle name={['data', item.id]}>
          <Select
            allowClear
            bordered={false}
            className="w-2/3"
            placeholder={setting.tooltip}>
            {compose(
              map((x: Option) => (
                <Select.Option key={x.id} value={x.text || ''}>
                  {x.text || ''}
                </Select.Option>
              )),
              pathOr([], ['options'])
            )(setting)}
          </Select>
        </Form.Item>
      </TemplateWrapper>
    </>
  )
}
