import { pathOr, length } from 'ramda'
import useLazyQuery from '@/hooks/useQuery'
import { QueryVariables } from '@/constants/types'

export function useRadlexes(variables: QueryVariables = {}) {
  const { data, ...rest } = useLazyQuery<string[]>(
    `/api/v1/c/getCodeBooksByKeyword`,
    {
      variables,
      method: 'POST',
      lazy: false,
    }
  )

  const _data = pathOr(
    [
      { code: 1, value: 'Couinaud hepatic segment II' },
      { code: 2, value: 'Couinaud hepatic segment III' },
      { code: 3, value: 'Couinaud hepatic segment VI' },
      { code: 4, value: 'Couinaud hepatic segment V' },
      { code: 5, value: 'Couinaud hepatic segment VI' },
      { code: 6, value: 'Couinaud hepatic segment VII' },
      { code: 7, value: 'Couinaud hepatic segment VIII' },
      // { id: 8, code: 'RID12348' },
      // { id: 9, code: 'RID12349' },
    ],
    [],
    data
  )

  const dataSource = {
    content: _data,
    total: length(_data),
    totalPages: Math.floor(length(_data) / 12) + 1,
  }

  return {
    ...rest,
    data,
    dataSource,
  }
}
