/**
 * NOTE:
 * nerver using index as list component key forever,
 * it will completely fucked up your page
 */
import React from 'react'
import { withRouter } from 'react-router-dom'
import { DropTarget, ConnectDropTarget, XYCoord } from 'react-dnd'
import {
  equals,
  length,
  remove,
  update,
  insert,
  cond,
  gt,
  lt,
  T,
  __,
  add,
  pathOr,
  identity,
  always,
} from 'ramda'
import cx from 'classnames'
import { uuid } from '@/utils/webHelper'
import ItemTypes from '@/constants/item-types'
import { SnippetProps } from '@/constants/types'
import Snippet from '@/components/snippet'
import BlockPlaceholder from '@/components/snippet-block-placeholder'
import { canMoveSnippet, getPlaceHolderIndex } from './snippet-utils'
const { compose } = require('ramda')

interface Props {
  draggingItem: any
  isOver?: boolean
  draggable?: boolean
  canDrop?: boolean
  snippetList: SnippetProps[]
  connectDropTarget: ConnectDropTarget
  onSnippetUpdate(data: any): void
}

interface State {
  placeholderIndex: number | null
}

const withDropTarget = DropTarget<Props>(
  ItemTypes.BOARD,
  {
    drop(_, monitor, component) {
      if (!component || !component.props.draggable) {
        return
      }

      const canDrop = monitor.canDrop()
      let { placeholderIndex } = component.state
      const item = monitor.getItem()

      if (canDrop && item.fromMenu) {
        component.handleSnippetDrop({ ...item, placeholderIndex })
      }

      if (canDrop && !item.fromMenu) {
        component.handleSnippetMove(item.id, placeholderIndex)
      }
    },
    canDrop({ snippetList }, monitor) {
      // if there is no items in cards, and is over on the board currently,
      // we can display a placeholder and drop the snippet item on the board.
      if (snippetList.length === 0 && monitor.isOver()) {
        return true
      }

      const clientOffset = monitor.getClientOffset() as XYCoord
      const element = document.elementFromPoint(clientOffset.x, clientOffset.y)

      return canMoveSnippet(element)
    },
    hover(_, monitor, component) {
      // Determine mouse position
      const clientOffset = monitor.getClientOffset() as XYCoord

      let {
        placeholderIndex,
        isOverSnippet,
        overSnippet,
      } = getPlaceHolderIndex(clientOffset)

      // 還要判斷是否是在最後一個 snippet 下面及 snippets-container 裡面
      if (!isOverSnippet) {
        return
      }

      const hoverBoundingRect = (overSnippet as HTMLElement).getBoundingClientRect()
      const hoverMiddleY =
        (hoverBoundingRect.bottom - hoverBoundingRect.top) / 2
      const hoverClientY = clientOffset.y - hoverBoundingRect.top

      if (hoverClientY < hoverMiddleY) {
        placeholderIndex -= 1
      }

      if (
        monitor.canDrop() &&
        !equals(component.state.placeholderIndex, placeholderIndex)
      ) {
        component.setState({ placeholderIndex })
      }
    },
  },
  (connect, monitor) => ({
    connectDropTarget: connect.dropTarget(),
    isOver: monitor.isOver(),
    canDrop: monitor.canDrop(),
    didDrop: monitor.didDrop(),
    draggingItem: monitor.getItem(),
  })
)

class SnippetsContainer extends React.Component<Props, State> {
  static defaultProps = {
    draggable: true,
    snippetList: [],
  }

  public state: State = {
    placeholderIndex: null,
  }

  handleSnippetMove = (id: React.Key, atIndex: number) => {
    const { snippetList } = this.props
    const { snippet, index } = this.handleFindSnippet(id)

    const newList = []
    let isPlaced = false

    snippetList.forEach((s, idx) => {
      if (idx === index) {
        return
      }

      if (!isPlaced && atIndex < idx) {
        newList.push(snippet)
        isPlaced = true
      }

      newList.push(s)

      if (!isPlaced && atIndex === idx) {
        newList.push(snippet)
        isPlaced = true
      }
    })

    // we dropped the only one snippet in the list so let it back to list.
    if (length(snippetList) === 1 && !atIndex) {
      newList.push(snippet)
    }

    this.props.onSnippetUpdate(newList)
    this.setState({ placeholderIndex: null })
  }

  // prettier-ignore
  handleSnippetDrop = (droppedItem: { [key: string]: any; placeholderIndex: number }) => {
    const { placeholderIndex, ...item } = droppedItem

    const snippetList = [...this.props.snippetList]

    const nextIndex = placeholderIndex + 1
    let newList: any[] = []

    const snippet: Partial<SnippetProps> = {
      ...item,
      fromMenu: false,
      id: `${item.id}__${uuid()}`,
    }

    if (length(snippetList) === 0) {
      newList = insert(0, snippet)(snippetList)
    }
    if (length(snippetList) > 0) {
      newList = insert(nextIndex, snippet)(snippetList)
    }

    this.props.onSnippetUpdate(newList)
    this.setState({ placeholderIndex: null })
  }

  handleSnippetRemove = (index: number) => {
    this.props.onSnippetUpdate(remove(index, 1)(this.props.snippetList))
  }

  handleSnippetShift = (id: React.Key, shift: number) => {
    const { snippet } = this.handleFindSnippet(id)

    this.handleSnippetUpdate({
      ...snippet,
      tabSize: compose(
        cond([
          [gt(__, 2), always(2)],
          [lt(__, 0), always(0)],
          [T, identity],
        ]),
        add(shift),
        pathOr(0, ['tabSize'])
      )(snippet),
    })
  }

  handleSnippetUpdate = (data: any) => {
    const { snippet, index } = this.handleFindSnippet(data.id)

    this.props.onSnippetUpdate(
      update(index, { ...snippet, ...data })(this.props.snippetList)
    )
  }

  handleFindSnippet = (id: React.Key) => {
    const { snippetList } = this.props
    const snippet = snippetList.filter(x => x.id === id)[0]

    return {
      snippet,
      index: snippetList.indexOf(snippet),
    }
  }

  render() {
    const {
      connectDropTarget,
      isOver,
      canDrop,
      snippetList,
      draggingItem,
      draggable, // 模版是否可拖曳，編輯模式下才會是 true
    } = this.props

    const { placeholderIndex } = this.state

    let isPlaceHold = false
    const cardList = []

    snippetList.forEach((item, idx) => {
      if (isOver && canDrop) {
        isPlaceHold = false
        if (idx === 0 && placeholderIndex === -1) {
          cardList.push(<BlockPlaceholder key={`block-placeholder-${idx}`} />)
        } else if ((placeholderIndex as any) > idx) {
          isPlaceHold = true
        }
      }

      if (item !== undefined) {
        cardList.push(
          <Snippet
            key={item.id}
            id={item.id}
            index={idx}
            draggable={draggable}
            item={item}
            inline={item.inline}
            // wrapperId={(item.id || `snippet-wrapper-${idx}`) as string}
            wrapperId={`snippet-wrapper-${idx}`}
            tabSize={item.tabSize}
            findSnippet={this.handleFindSnippet}
            updateSnippet={this.handleSnippetUpdate}
            shiftSnippet={this.handleSnippetShift}
            removeSnippet={this.handleSnippetRemove}>
            {item.inline ? (
              <div
                id={`${item.id}`}
                className={`${item.id}`}
                css={{ padding: 16, lineHeight: 2 }}
              />
            ) : (
              <item.Component
                id={item.id}
                className={item.id}
                item={item}
                index={idx}
                onUpdate={this.handleSnippetUpdate}
              />
            )}
          </Snippet>
        )
      }

      if (isOver && canDrop && placeholderIndex === idx) {
        cardList.push(<BlockPlaceholder key={`block-placeholder-${idx}`} />)
      }
    })

    // if placeholder index is greater than array.length, display placeholder as last
    if (isPlaceHold) {
      cardList.push(<BlockPlaceholder key={'block-placeholder-bottom'} />)
    }

    // if there is no items in cards currently, display a placeholder anyway
    if (isOver && canDrop && snippetList.length === 0) {
      cardList.push(<BlockPlaceholder key={'block-placeholder-zero'} />)
    }

    // if just has only one item in list and currently dragged, display a placeholder
    if (isOver && canDrop && snippetList.length === 1) {
      if (!draggingItem.fromMenu) {
        cardList.push(<BlockPlaceholder key={'block-placeholder-zero'} />)
      }
    }

    return connectDropTarget(
      <div
        className={cx('report-template-builder__contentarea', {
          'report-template-builder__contentarea--fixed-layout': !draggable,
        })}>
        {cardList}
      </div>
    )
  }
}

export default compose(withRouter, withDropTarget)(SnippetsContainer)
