/* eslint eqeqeq: 0 */
import React from 'react'

export type IReactComponent<P = any> =
  | React.StatelessComponent<P>
  | React.ComponentClass<P>
  | React.ClassicComponentClass<P>

function computeHeight(node: HTMLDivElement) {
  const totalHeight = parseInt(getComputedStyle(node).height, 10)
  const padding =
    parseInt(getComputedStyle(node).paddingTop, 10) +
    parseInt(getComputedStyle(node).paddingBottom, 10)
  return totalHeight - padding
}

function getAutoHeight(n: HTMLDivElement | undefined) {
  if (!n) {
    return 0
  }

  let node = n

  let height = computeHeight(node)

  while (!height) {
    node = node.parentNode as HTMLDivElement
    if (node) {
      height = computeHeight(node)
    } else {
      break
    }
  }

  return height
}

interface AutoHeightProps {
  height?: number
}

function autoHeight() {
  return <P extends AutoHeightProps>(
    WrappedComponent: React.ComponentClass<P> | React.FC<P>
  ) =>
    class extends React.Component<P & AutoHeightProps> {
      state = {
        computedHeight: 0,
      }

      root: HTMLDivElement | undefined = undefined

      componentDidMount() {
        const { height } = this.props
        if (!height) {
          const h = getAutoHeight(this.root)
          // eslint-disable-next-line
          this.setState({ computedHeight: h })
        }
      }

      handleRoot = (node: HTMLDivElement) => {
        this.root = node
      }

      render() {
        const { height } = this.props
        const { computedHeight } = this.state
        const h = height || computedHeight
        return (
          <div ref={this.handleRoot}>
            {h > 0 && <WrappedComponent {...this.props} height={h} />}
          </div>
        )
      }
    }
}

export default autoHeight
