import React from 'react'
import { DragSource, DropTarget } from 'react-dnd'
import cx from 'classnames'
import { compose } from 'ramda'
import Tabs from '@/components/tabs'

// Drag & Drop node
class TabNode extends React.Component {
  render() {
    const {
      connectDragSource,
      connectDropTarget,
      isDragging,
      isOver,
      moveTabNode,
      draggingItem,
      id,
      ...props
    } = this.props

    return connectDragSource(
      connectDropTarget(
        React.cloneElement(props.children, {
          className: cx(props.children.props.className, {
            'is--dragging': isDragging,
            'is--over': isOver && id !== draggingItem?.id,
          }),
        })
      )
    )
  }
}

const withDropTarget = DropTarget(
  'DND_NODE',
  {
    drop(props, monitor) {
      const dragKey = monitor.getItem().index
      const hoverKey = props.index

      if (dragKey === hoverKey) {
        return
      }

      props.moveTabNode(dragKey, hoverKey)
      monitor.getItem().index = hoverKey
    },
  },
  (connect, monitor) => ({
    connectDropTarget: connect.dropTarget(),
    isOver: monitor.isOver(),
    draggingItem: monitor.getItem(),
  })
)

const withDragSource = DragSource(
  'DND_NODE',
  {
    beginDrag(props) {
      return {
        id: props.id,
        index: props.index,
      }
    },
  },
  (connect, monitor) => ({
    connectDragSource: connect.dragSource(),
    isDragging: monitor.isDragging(),
  })
)

const WrapTabNode = compose(withDropTarget, withDragSource)(TabNode)

export default class DraggableTabs extends React.Component {
  static defaultProps = {
    order: [],
  }

  moveTabNode = (dragKey, hoverKey) => {
    const { children, order, moveTabNode } = this.props
    const newOrder = order.slice()

    React.Children.forEach(children, c => {
      if (newOrder.indexOf(c.key) === -1) {
        newOrder.push(c.key)
      }
    })

    const dragIndex = newOrder.indexOf(dragKey)
    const hoverIndex = newOrder.indexOf(hoverKey)

    newOrder.splice(dragIndex, 1)
    newOrder.splice(hoverIndex, 0, dragKey)

    moveTabNode(newOrder)
  }

  renderTabBar = (props, DefaultTabBar) => (
    <DefaultTabBar {...props}>
      {node => (
        <WrapTabNode
          key={node.key}
          id={node.key}
          index={node.key}
          moveTabNode={this.moveTabNode}>
          {node}
        </WrapTabNode>
      )}
    </DefaultTabBar>
  )

  render() {
    const { moveTabNode, children, ...props } = this.props

    return (
      <Tabs renderTabBar={this.renderTabBar} {...props}>
        {children}
      </Tabs>
    )
  }
}

DraggableTabs.TabPane = Tabs.TabPane
