import moment from 'moment'
import { envStore } from '@/constants/env'
const {
  forEach,
  addIndex,
  map,
  isEmpty,
  either,
  compose,
  not,
  isNil: RisNil,
  path,
  is,
} = require('ramda')

export function queryString(search?: string): { [key: string]: any } {
  if (!search) {
    search = window.location.search
  }

  const params = new URLSearchParams(search)

  let result: object | any = {}

  params.forEach((value: string, key: string) => (result[key] = value))

  return result
}

export function uuid() {
  let d = Date.now()
  if (
    typeof performance !== 'undefined' &&
    typeof performance.now === 'function'
  ) {
    d += performance.now()
  }
  return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, c => {
    const r = (d + Math.random() * 16) % 16 | 0
    d = Math.floor(d / 16)
    return (c === 'x' ? r : (r & 0x3) | 0x8).toString(16)
  })
}

export function isUUID(uuid: string) {
  return /^[0-9a-f]{8}-[0-9a-f]{4}-[0-5][0-9a-f]{3}-[089ab][0-9a-f]{3}-[0-9a-f]{12}$/i.test(
    uuid
  )
}

export const forEachIndexed = addIndex(forEach)
export const mapIndexed: any = addIndex(map)

export const isNil = either(isEmpty, RisNil)

export const hasPath = (namedPath: Array<string | number>) =>
  compose(not, isNil, path(namedPath))

export const getRangeDate = (
  date: [moment.Moment, moment.Moment],
  dateFormat?: string
) => {
  const [startDate, stopDate] = date

  return [
    startDate
      ? dateFormat
        ? startDate.format(dateFormat)
        : startDate.valueOf()
      : undefined,
    stopDate
      ? dateFormat
        ? stopDate.format(dateFormat)
        : stopDate.valueOf()
      : undefined,
  ]
}

export const toThousandSeparator = (number: number | string) => {
  if (number && (typeof number === 'number' || isNaN(number as any))) {
    return number.toLocaleString()
  }
  return number
}

export const capitalize = (s: string) => {
  if (typeof s !== 'string') return ''
  return s.charAt(0).toUpperCase() + s.slice(1)
}

export function getNumberFromString(str?: string) {
  if (!str) {
    return str
  }
  return str.replace(/[^\d.]/g, '')
}

export const redirect = () => {
  if (!envStore.isEnvDev && window.location.pathname === '/') {
    window.location.href = envStore.appUrl
    return
  }
}

export function downloadAsJson({
  json,
  filename,
}: {
  json: object
  filename: string
}) {
  const dataStr =
    'data:text/json;charset=utf-8,' +
    encodeURIComponent(JSON.stringify(json, null, 2))
  const downloadAnchorNode = document.createElement('a')
  downloadAnchorNode.setAttribute('href', dataStr)
  downloadAnchorNode.setAttribute('download', filename + '.json')
  document.body.appendChild(downloadAnchorNode) // required for firefox
  downloadAnchorNode.click()
  downloadAnchorNode.remove()

  return json
}

export function selectFile({
  accept = '*',
  callback,
}: {
  accept?: string
  callback?: (files: FileList | null) => any
}) {
  const input = document.createElement('input')
  input.setAttribute('type', 'file')
  input.setAttribute('class', 'hidden')
  input.setAttribute('accept', accept)

  if (callback) {
    input.addEventListener('change', () => callback(input.files))
  }

  input.click()
}

export async function readFile<T = any>(file: File): Promise<T> {
  return new Promise((resolve, reject) => {
    const fileReader = new FileReader()
    fileReader.addEventListener('load', () => resolve(fileReader.result as any))
    fileReader.onerror = reject
    fileReader.readAsText(file)
  })
}

export const toMoment = (data: any) => {
  if (!data) {
    return data
  }

  if (moment.isMoment(data)) {
    return data
  }

  let result

  if (is(Number, data) || is(String, data) || is(Date, data)) {
    result = moment(data)
  }

  if (result?.isValid()) {
    return result
  }

  return data
}

export const toDateString = (
  data: any,
  format: string = 'YYYY.MM.DD HH:mm:ss'
) => {
  const m = toMoment(data)

  if (moment.isMoment(m)) {
    return m.format(format)
  }

  return ''
}

export const sorter = (field: string) => (a: any, b: any) =>
  `${a[field]}`.localeCompare(b[field])
