import React from 'react'
import styled from 'styled-components'
import {
  EditOutlined,
  DeleteOutlined,
  RightOutlined,
  LeftOutlined,
} from '@ant-design/icons'
import cx from 'classnames'
import { Tooltip } from 'antd'

type Props = {
  visible?: boolean
  editable?: boolean
  tabIndex: number
  onEdit: () => void
  onDelete: () => void
  onShift: (shift: number) => void
}

const ToolbarWrapper = styled.div`
  position: absolute;
  top: 8px;
  right: 15px;
  visibility: hidden;
  transition: visibility 0s, opacity 0.3s, max-height 0.6s linear;
  opacity: 0;
  z-index: 1;

  &.is--over {
    visibility: visible;
    transition: visibility 0s, opacity 0.3s, max-height 0.6s linear;
    opacity: 0.8;
  }
`

export const SHIFT_TYPES = {
  LEFT: -1,
  RIGHT: 1,
}

export default function SnippetToolbar({
  tabIndex,
  onEdit,
  onDelete,
  onShift,
  editable = true,
  visible = false,
}: Props) {
  const handleShift = (shift: number) => () => {
    onShift(shift)
  }

  return (
    <ToolbarWrapper
      className={cx('text-primary', {
        'is--over': tabIndex === 1,
        hidden: !visible,
      })}>
      <Tooltip title="向左縮排">
        <LeftOutlined
          className="mr-2 cursor-pointer"
          onClick={handleShift(SHIFT_TYPES.LEFT)}
        />
      </Tooltip>
      <Tooltip title="向右縮排">
        <RightOutlined
          className="mr-2 cursor-pointer"
          onClick={handleShift(SHIFT_TYPES.RIGHT)}
        />
      </Tooltip>
      {editable && (
        <Tooltip title="編輯">
          <EditOutlined className="mr-2 cursor-pointer" onClick={onEdit} />
        </Tooltip>
      )}
      <Tooltip title="刪除">
        <DeleteOutlined className="cursor-pointer" onClick={onDelete} />
      </Tooltip>
    </ToolbarWrapper>
  )
}

export { SnippetToolbar }
