import React from 'react'
// import { isNil } from 'ramda'
import Cookies from 'js-cookie'
import { Divisions } from '@/constants/enums'
import { User } from '@/constants/types'

export type LogoutFn = (params?: { onCompleted?: () => any }) => any

export type AuthContextType = {
  logout: LogoutFn
  isAuthenticated: boolean
  user: User
  setUser: React.Dispatch<React.SetStateAction<User | null>>
  setIsAuthenticated: React.Dispatch<React.SetStateAction<boolean>>
}

export const AuthContext = React.createContext<AuthContextType>({
  isAuthenticated: false,
  user: {} as User,
  logout: () => ({}),
  setUser: (_: React.SetStateAction<User | null>) => ({}),
  setIsAuthenticated: (_: React.SetStateAction<boolean>) => ({}),
})

const _user = Cookies.getJSON('vghks_user')
// const _token = Cookies.get('vghks_token')

export function AuthProvider(props: any) {
  const [isAuthenticated, setIsAuthenticated] = React.useState(
    // !isNil(_user)
    true
  )

  const [user, setUser] = React.useState<User>({
    id: 1,
    name: '醫師姓名',
    division: Divisions.PATH,
    position: 'A',
    isAdmin: true,
    viewDivision: false,
    ..._user,
  })

  const logout = React.useCallback(({ onCompleted } = {}) => {
    Cookies.remove('vghks_token')
    Cookies.remove('vghks_user')

    setIsAuthenticated(false)

    if (onCompleted) {
      onCompleted()
    }
  }, [])

  const value = React.useMemo(
    () => ({
      isAuthenticated,
      setIsAuthenticated,
      setUser,
      logout,
      user,
    }),
    [isAuthenticated, logout, user, setUser, setIsAuthenticated]
  )

  return <AuthContext.Provider value={value} {...props} />
}

export function useAuth() {
  const context = React.useContext(AuthContext)
  if (context === undefined) {
    throw new Error(`useAuth must be used within a AuthProvider`)
  }
  return context
}

export default useAuth
