import React from 'react'
import styled from 'styled-components'
import cx from 'classnames'
import withScroll, { createVerticalStrength } from './withScroll'

interface Props {
  className?: string
  useScroll?: boolean
}

const DndScrollableElement = styled.div`
  height: 85vh;
  padding-top: 0;
  overflow-y: scroll;

  .report-template-builder {
    &__main-content {
      min-height: 371.25mm;
      margin: 0 auto;
      padding: 16px;
      box-sizing: border-box;
      max-width: 100%;
      background: #fff;
      border-top-right-radius: 16px;
      border-bottom-left-radius: 16px;
      border-bottom-right-radius: 16px;
    }

    &__contentarea {
      min-height: 348.75mm;
    }

    &__contentarea--fixed-layout {
      min-height: auto;
    }

    &__snippet-toolbar {
      visibility: hidden;
    }
  }

  .ui-draggable {
    &:focus,
    &:active {
      outline: 0;

      .course-builder__snippet-toolbar {
        visibility: visible;
        opacity: 1;
        transition: visibility 0s, opacity 0.3s, max-height 0.6s linear;
      }
    }
  }

  &.is--preview {
    height: auto;
  }
`

const ScrollableComponent = withScroll(DndScrollableElement)

const vStrength = createVerticalStrength(150)

export default class ReactDndProvider extends React.Component<Props> {
  static defaultProps = {
    useScroll: true,
  }

  render() {
    const { className, useScroll } = this.props

    return (
      <ScrollableComponent
        useScroll={useScroll}
        verticalStrength={vStrength}
        strengthMultiplier={100}
        className={cx('report-template-builder', className)}>
        {this.props.children}
      </ScrollableComponent>
    )
  }
}
