import './bootstrap'
import React from 'react'
import ReactDOM from 'react-dom'
import { Router } from 'react-router-dom'
import { DndProvider } from 'react-dnd'
import { HTML5Backend } from 'react-dnd-html5-backend'
import { RecoilRoot, MutableSnapshot } from 'recoil'
import { ConfigProvider } from 'antd'
import zhTW from 'antd/lib/locale/zh_TW'
import { ThemeProvider } from 'styled-components'
import { SWRConfig } from 'swr'
import request from '@/utils/request'
import history from '@/utils/history'
import { createPack } from '@/utils/createPack'
import { redirect } from '@/utils/webHelper'
import { AuthProvider, useAuth } from '@/hooks/useAuth'
import App from './app'
import theme from './theme'
import * as serviceWorker from './serviceWorker'
import './index.css'

redirect()

const initializeState = ({ set }: MutableSnapshot) => {
  const { user } = (useAuth as any)()
  set({ key: 'userState' } as any, user)
  // for(const [key, value] of Storage.entries()) {
  //   set(myLookupOfAtomWithKey(key), JSON.parse(value).value);
  // }
}

const EnhancedApp = createPack(
  <ThemeProvider theme={theme} />,
  <ConfigProvider locale={zhTW} />,
  <AuthProvider />,
  <RecoilRoot initializeState={initializeState} />,
  <SWRConfig
    value={{ suspense: true, fetcher: request, revalidateOnFocus: false }}
  />,
  <DndProvider backend={HTML5Backend} />,
  <Router history={history} />
)

// ReactDOM.unstable_createRoot(document.getElementById('root') as any).render(
//   <EnhancedApp>
//     <App />
//   </EnhancedApp>
// )
ReactDOM.render(
  <EnhancedApp>
    <App />
  </EnhancedApp>,
  document.getElementById('root')
)

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister()
