import React from 'react'
import { Form } from 'antd'
import Input from '@/components/input'
import InputNumber from '@/components/input-number'
import { Modal, ModalProps } from '@/components/snippet-template-modal'
import TemplateWrapper from '@/components/snippet-template-wrapper'
import { Radlex, useRadlex } from '@/components/radlex-list-modal'
import { createFormItemLayout } from '@/utils/form'
import { ReportTemplateSetting, ReportTemplateProps } from '@/constants/types'
import { useUpdateSnippet } from '@/services/report-template'
import { getNumberFromString } from '@/utils/webHelper'
import { is } from 'ramda'

interface Setting extends ReportTemplateSetting {
  defaultValue?: number
  max?: number
  min?: number
  units?: string
}

export default function SnippetTemplateInputNumber(props: ReportTemplateProps) {
  const { editModalVisible, handleEditModalVisible, item, onUpdate } = props

  const { setting, handleOk } = useUpdateSnippet<Setting>({ item, onUpdate })

  return (
    <>
      {editModalVisible && (
        <InputNumberModal
          item={setting}
          onCancel={handleEditModalVisible}
          onOk={handleOk}
        />
      )}
      <TemplateWrapper title={setting.text || ''}>
        <Form.Item
          noStyle
          name={['data', item.id]}
          initialValue={setting.defaultValue}>
          <InputNumber
            style={{ width: '33.333333%' }}
            className="ant-input-number-borderless"
            min={setting.min}
            max={setting.max}
            formatter={(value: any) => {
              if (!setting.units) {
                return value
              }
              if (value && is(String, value) && value.includes(setting.units)) {
                return `${getNumberFromString(value)} ${setting.units}`
              }
              return `${value} ${setting.units}`
            }}
            parser={(value: any) =>
              setting.units
                ? value.replace(new RegExp(`/s${setting.units}/g`), '')
                : value
            }
            placeholder={setting.tooltip}
          />
        </Form.Item>
      </TemplateWrapper>
    </>
  )
}

function InputNumberModal({ onOk, onCancel, item, ...props }: ModalProps) {
  const [form] = Form.useForm()

  const handleOk = () => {
    form.validateFields().then(values => {
      if (onOk) {
        onOk(values)
      }
      onCancel()
    })
  }

  const handleSelect = useRadlex(onOk)

  return (
    <Modal
      {...props}
      title={
        <>
          <div>
            Number
            <div className="inline ml-2 text-sm text-gray-600">
              typically used for input number.
            </div>
          </div>
        </>
      }
      onOk={handleOk}
      onCancel={onCancel}>
      <Form
        {...createFormItemLayout(5, 16)}
        labelAlign="left"
        form={form}
        initialValues={item}>
        <Radlex item={item} onSelect={handleSelect} />
        <Form.Item label="Default Value" name="defaultValue">
          <Input />
        </Form.Item>
        <Form.Item label="Tooltip" name="tooltip">
          <Input />
        </Form.Item>
        <Form.Item label="Minimum Value" name="min">
          <InputNumber />
        </Form.Item>
        <Form.Item label="Maximum Value" name="max">
          <InputNumber />
        </Form.Item>
        <Form.Item label="Units" name="units">
          <Input />
        </Form.Item>
      </Form>
    </Modal>
  )
}
