export type RouteItem = {
  id: number
  title: string
  path: string
  component: typeof React.Component
  icon?: React.FunctionComponent
  disabled?: boolean
  param?: string
  children?: RouteItem[]
  hide?: RouteItem[]
}

export type Route = RouteItem & {
  children: Array<RouteItem>
  hide: Array<RouteItem>
  disabled: boolean
}

