import React from 'react'
import cx from 'classnames'
import { Layout, Menu, Select } from 'antd'
import {
  SettingOutlined,
  ExportOutlined,
  HomeOutlined,
  CaretDownOutlined,
} from '@ant-design/icons'
import styled from 'styled-components'
import Button from '@/components/button'
import Breadcrumb from '@/components/breadcrumb'
import LogoutConfirmModal from '@/components/logout-confirm-modal'
import { useBreadcrumbState, useHeaderContentValue } from '@/hooks/useApp'
import useAuth from '@/hooks/useAuth'
import useVisible from '@/hooks/useVisible'
import history from '@/utils/history'
import { envStore } from '@/constants/env'
import * as routes from '@/constants/routes'
import { Divisions } from '@/constants/enums'

export default function AppHeader() {
  const [items] = useBreadcrumbState()
  const content = useHeaderContentValue()

  const { logout, user, setUser } = useAuth()

  const [current, setCurrentPath] = React.useState(history.location.pathname)

  const [logoutModalVisible, handleLogoutModalVisible] = useVisible()

  const handleLogoClick = () => {
    if (user.isAdmin) {
      setUser((prev: any) => ({ ...prev, viewDivision: false }))
    }
    history.push('/')
  }

  const handleTo = (pathname?: string) => () => {
    if (pathname) {
      setCurrentPath(pathname)
      history.push(pathname)
    }
  }

  const getMenuClassName = (targetUrl: string) => {
    return cx({ 'is--current': current !== '/' && targetUrl.includes(current) })
  }

  const handleUserChange = (value: string) => {
    setUser(prev => ({ ...prev, division: value } as any))
  }

  const handleRoleChange = (value: number) => {
    setUser((prev: any) => ({ ...prev, isAdmin: !!value, viewDivision: false }))
    history.push('/')
  }

  const showMenu = user.division !== 'CLINICIAN'

  return (
    <>
      {logoutModalVisible && (
        <LogoutConfirmModal
          onOk={logout as any}
          onCancel={handleLogoutModalVisible}
        />
      )}
      <Header>
        <div className="flex items-center justify-between">
          <div className="flex items-center">
            <Logo onClick={handleLogoClick}>
              <img
                src={`${envStore.publicUrl}/images/logo-sm@2x.png`}
                alt="app-logo"
                className="cursor-pointer"
              />
            </Logo>
            {showMenu && (
              <Menu
                mode="horizontal"
                className="bg-transparent ml-3"
                style={{ borderBottom: 'none', lineHeight: '46px' }}>
                {user.division === 'PATH' && (
                  <MenuItem
                    key={routes.IMAGE_SCHEDULER}
                    onClick={handleTo(routes.IMAGE_SCHEDULER)}
                    className={getMenuClassName(routes.IMAGE_SCHEDULER)}>
                    <div className="flex items-center text-base">
                      <SettingOutlined />
                      報告排班
                    </div>
                  </MenuItem>
                )}
                {user.division === 'RAD' && (
                  <MenuItem
                    key={routes.REPORT_LIST}
                    onClick={handleTo(routes.REPORT_LIST)}
                    className={getMenuClassName(routes.REPORT_LIST)}>
                    <div className="flex items-center text-base">
                      <SettingOutlined />
                      報告查詢
                    </div>
                  </MenuItem>
                )}
                {user.division === 'PATH' && (
                  <SubMenu
                    key={routes.REPORT_LIST}
                    className={getMenuClassName(routes.REPORT_LIST)}
                    popupOffset={[-14, 0]}
                    popupClassName="rounded"
                    title={
                      <div className="flex items-center text-base">
                        <SettingOutlined />
                        報告查詢
                        <CaretDownOutlined className="ml-2 mr-0" />
                      </div>
                    }>
                    <Menu.Item
                      key={routes.REPORT_LIST}
                      onClick={handleTo(routes.REPORT_LIST)}
                      className={getMenuClassName(routes.REPORT_LIST)}>
                      <div className="flex items-center text-sm">
                        <SettingOutlined />
                        {/* {user.isAdmin ? '病理報告查詢' : '報告查詢'} */}
                        報告查詢
                      </div>
                    </Menu.Item>
                    {/* {user.isAdmin && (
                      <Menu.Item
                        key={routes.RADIOL_REPORT_LIST}
                        onClick={handleTo(routes.RADIOL_REPORT_LIST)}
                        className={getMenuClassName(routes.RADIOL_REPORT_LIST)}>
                        <div className="flex items-center text-sm">
                          <SettingOutlined />
                          放射報告查詢
                        </div>
                      </Menu.Item>
                    )} */}
                    <Menu.Item
                      key={routes.REPORT_VERSION_COMPARISON}
                      onClick={handleTo(routes.REPORT_VERSION_COMPARISON)}
                      className={getMenuClassName(
                        routes.REPORT_VERSION_COMPARISON
                      )}>
                      <div className="flex items-center text-sm">
                        <SettingOutlined />
                        版本比較
                      </div>
                    </Menu.Item>
                    <Menu.Item
                      key={routes.YELLOW_CARD_INQUIRY}
                      onClick={handleTo(routes.YELLOW_CARD_INQUIRY)}
                      className={getMenuClassName(routes.YELLOW_CARD_INQUIRY)}>
                      <div className="flex items-center text-sm">
                        <SettingOutlined />
                        黃卡新檢索
                      </div>
                    </Menu.Item>
                    <Menu.Item
                      key={routes.BLOOD_BANK_REVIEW}
                      onClick={handleTo(routes.BLOOD_BANK_REVIEW)}
                      className={getMenuClassName(routes.BLOOD_BANK_REVIEW)}
                      style={{ display: 'none' }}>
                      <div className="flex items-center text-sm">
                        <SettingOutlined />
                        血庫備血審查作業
                      </div>
                    </Menu.Item>
                  </SubMenu>
                )}
                <MenuItem
                  key={routes.REPORT_INQUIRY}
                  onClick={handleTo(routes.REPORT_INQUIRY)}
                  className={getMenuClassName(routes.REPORT_INQUIRY)}>
                  <div className="flex items-center text-base">
                    <SettingOutlined />
                    輸入報告
                  </div>
                </MenuItem>
                <SubMenu
                  key={routes.REPORT_TEMPLATE_LIST}
                  className={getMenuClassName(routes.REPORT_TEMPLATE_LIST)}
                  popupOffset={[-14, 0]}
                  popupClassName="rounded"
                  title={
                    <div className="flex items-center text-base">
                      <SettingOutlined />
                      報告模版
                      <CaretDownOutlined className="ml-2 mr-0" />
                    </div>
                  }>
                  <Menu.Item
                    key={routes.REPORT_TEMPLATE_LIST}
                    onClick={handleTo(routes.REPORT_TEMPLATE_LIST)}
                    className={getMenuClassName(routes.REPORT_TEMPLATE_LIST)}>
                    <div className="flex items-center text-sm">
                      <SettingOutlined />
                      結構式報告模版
                    </div>
                  </Menu.Item>
                  <Menu.Item
                    key={routes.PHRASE_LIST}
                    onClick={handleTo(routes.PHRASE_LIST)}
                    className={getMenuClassName(routes.PHRASE_LIST)}>
                    <div className="flex items-center text-sm">
                      <SettingOutlined />
                      片語模版
                    </div>
                  </Menu.Item>
                </SubMenu>
                <MenuItem
                  key={routes.IMAGE_VIEWER_TUTORIAL}
                  onClick={handleTo(routes.IMAGE_VIEWER_TUTORIAL)}
                  className={getMenuClassName('outside')}>
                  <div className="flex items-center text-base">
                    <SettingOutlined />
                    醫學影像教學平台
                  </div>
                </MenuItem>
              </Menu>
            )}
          </div>
          <div className="flex items-center">
            <Select
              bordered={false}
              placeholder="管理者"
              className="text-white"
              style={{ width: 96, marginRight: -30 }}
              suffixIcon={false}
              defaultValue={user.isAdmin ? 1 : 0}
              onChange={handleRoleChange}>
              <Select.Option value={1}>管理者</Select.Option>
              <Select.Option value={0}>使用者</Select.Option>
            </Select>
            <Select
              bordered={false}
              placeholder="切換身分"
              className="text-white"
              style={{ width: 100, marginRight: -20 }}
              suffixIcon={false}
              defaultValue={user.division}
              onChange={handleUserChange}>
              <Select.Option value={Divisions.RAD}>放射科</Select.Option>
              <Select.Option value={Divisions.PATH}>病理科</Select.Option>
              <Select.Option value={Divisions.CLINICIAN}>臨床</Select.Option>
            </Select>
            <div className="text-white mr-1">Hi, {user.name}</div>
            {/* <Avatar className="flex items-center h-12 w-12">
              <div className="text-2xl">C</div>
            </Avatar> */}
            <Button
              icon={
                <ExportOutlined
                  className="text-white text-2xl"
                  style={{ marginRight: 0 }}
                />
              }
              onClick={handleLogoutModalVisible}
              className="ml-4 bg-transparent border border-white rounded pr-1 hover--none"
              style={{ minWidth: 35, borderColor: '#fff' }}
            />
          </div>
        </div>
        <div className="mt-3 ml-4 pb-0 flex justify-between">
          <Breadcrumb
            separator=">"
            style={{ minHeight: 32, display: 'flex', alignItems: 'center' }}>
            <Breadcrumb.Item onClick={handleTo('/')}>
              <HomeOutlined className="relative" style={{ top: -4 }} />
            </Breadcrumb.Item>
            {items.map((x, idx) => (
              <Breadcrumb.Item
                key={`breadcrumb-${idx}`}
                onClick={handleTo(x.href)}>
                {x.name}
              </Breadcrumb.Item>
            ))}
          </Breadcrumb>
          {content}
        </div>
      </Header>
    </>
  )
}

const Header = styled(Layout.Header)`
  &.ant-layout-header {
    height: auto;
    padding: 16px 1.7rem 10px 2.1rem;
    background: ${p => `linear-gradient(-90deg, #42c2b8, ${p.theme.primary})`};
    color: ${p => p.theme.primary};
    box-shadow: 0 0 20px ${p => p.theme.purple150};
  }

  .anticon {
    cursor: pointer;
  }
`

const MenuItem = styled(Menu.Item)`
  &.ant-menu-item {
    margin: 0 2px !important;
    padding: 0 10px !important;
    border-radius: 6px;
    border-bottom: none !important;
    color: #fff;

    &.is--current,
    &:hover {
      color: #fff !important;
      background: rgba(24, 144, 138, 1) !important;
    }
  }

  &.ant-menu-item-selected {
    color: #fff !important;
  }
`

const SubMenu = styled(Menu.SubMenu)`
  &.ant-menu-submenu {
    margin: 0 5px !important;
    padding: 0 15px !important;
    border-radius: 6px;
    border-bottom: none !important;

    .ant-menu-submenu-title {
      color: #fff;
    }

    &.is--current,
    &:hover {
      background: rgba(24, 144, 138, 1) !important;
    }
  }
`

const Logo = styled.div``
