import React from 'react'
import cx from 'classnames'
import { map } from 'ramda'
import { Form, Tooltip } from 'antd'
import { DoubleRightOutlined } from '@ant-design/icons'
import Collapse from '@/components/collapse'
import { Checkbox, CheckboxGroup } from '@/components/checkbox'
import Radio from '@/components/radio'
import { reportAnnotations } from '@/constants/enums'
import useAuth from '@/hooks/useAuth'
import { MedicalOrder } from '@/constants/types'
import { reportStatusEnums } from '@/constants/enums'

type Props = {
  data?: MedicalOrder | null
  doctor?: string
  onSave?: () => void
  onPhraseOpen?: () => void
  onToggle?: () => void
}

export default function ReportMedicalOrder({
  data,
  doctor,
  onToggle,
}: Props) {
  const { user } = useAuth()
  return (
    <Collapse
      activeKey={[1, 2, 3, 4]}
      expandIcon={() => null}
      className="shadow-2xl">
      <Collapse.Panel
        header={
          <div className="text-center relative">
            <Tooltip title="收合醫囑內容">
              <DoubleRightOutlined
                className="absolute"
                style={{ top: 5, left: 15 }}
                onClick={onToggle}
              />
            </Tooltip>
            醫囑內容
          </div>
        }
        key={1}>
        <div className="border-b border-gray-600 p-3">
          申請序號：{data?.orreqno}
        </div>
        {user.division === 'PATH' && (
          <div className="border-b border-gray-600 p-3">
            病理號：{data?.orworkno}
          </div>
        )}
        <div className="border-b border-gray-600 p-3">
          病患姓名：{data?.hnamec}
        </div>
        <div className="border-b border-gray-600 p-3">性別：{data?.hsex}</div>
        <div className="border-b border-gray-600 p-3">
          申請科別：{data?.csect}
        </div>
        <div className="border-b border-gray-600 p-3">
          病歷號：{data?.hhisnum}
        </div>
        <div className="border-b border-gray-600 p-3">
          病房：{data?.hnursta}
        </div>
        <div className="border-b border-gray-600 p-3">
          病房床號：{data?.hbed}
        </div>
        <div className="border-b border-gray-600 p-3">
          出生日期：{data?.hbirthdt}
        </div>
        <div className="border-b border-gray-600 p-3">
          醫囑名稱：{data?.orproced}
        </div>
        <div className="border-b border-gray-600 p-3">
          現在輸入：{user.name}
        </div>
        <div className="border-b border-gray-600 p-3">審核者：{user.name}</div>
      </Collapse.Panel>
      <Collapse.Panel
        header={<div className="text-center">儲存狀態</div>}
        key={2}>
        <Form.Item noStyle name="status">
          <Radio.Group className="pl-3">
            {map((s: { id: number; name: string }) => (
              <Radio key={s.id} className="block mb-1" value={s.id}>
                {s.name}
              </Radio>
            ))(reportStatusEnums)}
          </Radio.Group>
        </Form.Item>
      </Collapse.Panel>
      <Collapse.Panel
        header={<div className="text-center">報告註記</div>}
        key={3}>
        <Form.Item noStyle name="annotations">
          <CheckboxGroup>
            {reportAnnotations.map((x, idx) => (
              <Checkbox
                key={`annotations-${idx}`}
                className={cx('block ml-0 pl-2', {
                  'mt-1': idx > 0,
                })}
                defaultChecked={false}
                value={`${idx}`}>
                {x}
              </Checkbox>
            ))}
          </CheckboxGroup>
        </Form.Item>
      </Collapse.Panel>
      <Collapse.Panel
        header={<div className="text-center">操作熱鍵</div>}
        key={4}>
        <div className="pl-6">
          <code className="bg-yellow-400 px-1">CTRL＋W</code>,{' '}
          <code className="bg-yellow-400 px-1">CTRL+S</code> 儲存
        </div>
        <div className="pl-6 pt-2">
          <code className="bg-yellow-400 px-1">F9</code> 片語視窗
        </div>
      </Collapse.Panel>
    </Collapse>
  )
}
