import React from 'react'
import { ColumnProps } from 'antd/lib/table'
import { ModalProps } from 'antd/lib/modal'
import { flatten, pluck } from 'ramda'
import Modal from '@/components/modal'
import Table from '@/components/query-table'
import Button from '@/components/button'
import { usePhraseList } from '@/services/phrase'
import { PhraseTemplate, SnippetProps } from '@/constants/types'
import useTableRowSelection from '@/hooks/useTableRowSelection'
const { compose } = require('ramda')

interface Props extends Omit<ModalProps, 'onOk'> {
  onOk: (values: SnippetProps[]) => void
}

export default function PhraseListModal({ onOk, onCancel, ...props }: Props) {
  const { data } = usePhraseList()

  const { rowSelection, selectedRows } = useTableRowSelection()

  const handleOk = () => {
    onOk(compose(flatten, pluck('setting'))(selectedRows))
    ;(onCancel as any)()
  }

  const columns: ColumnProps<PhraseTemplate>[] = [
    {
      title: '模版名稱',
      dataIndex: 'name',
    },
    {
      title: '創作者',
      dataIndex: 'author',
    },
    {
      title: '狀態',
      dataIndex: 'statusName',
    },
    {
      title: '創作時間',
      dataIndex: 'createdAtStr',
    },
    {
      title: '上次修改時間',
      dataIndex: 'updatedAtStr',
    },
    {
      title: '模版描述',
      dataIndex: 'description',
    },
  ]

  return (
    <Modal
      {...props}
      visible
      title="選擇片語模版"
      bodyStyle={{
        paddingTop: 48,
        paddingLeft: 64,
        paddingRight: 64,
      }}
      width={800}
      footer={[
        <Button key="btn-cancel" color="outline" onClick={onCancel}>
          取消
        </Button>,
        <Button
          key="btn-ok"
          color="primary"
          onClick={handleOk}
          className="ml-3">
          確認
        </Button>,
      ]}
      onCancel={onCancel}
      keyboard={false}
      maskClosable={false}>
      <Table
        size="small"
        columns={columns}
        dataSource={data}
        rowSelection={rowSelection}
      />
    </Modal>
  )
}
