import React from 'react'
import { mergeAll, map, filter, keys, isNil, trim } from 'ramda'
import { QueryVariables } from '@/constants/types'
import useSWR from '@/hooks/useSWR'
import { request } from '@/utils/request'
const { compose } = require('ramda')

export default function useQuery<T = any>(
  url: string,
  {
    lazy = true,
    method = 'POST',
    variables: { onCompleted, onError, ...variables } = {},
  }: {
    method?: 'POST' | 'GET'
    variables?: QueryVariables
    lazy?: boolean
  }
) {
  const [query, setQuery] = React.useState(variables)

  const fetcher = (...args: any) =>
    request(url, {
      ...args,
      method,
      body: query,
      throwable: true,
    })

  const { data, refetch, ...rest } = useSWR<T>(
    url,
    {
      lazy,
      onCompleted,
      onError,
    },
    fetcher
  )

  const _refetch = (variables: QueryVariables = {}) => {
    const _variables = compose(
      mergeAll,
      map((key: string) => ({ [key]: variables[key] })),
      filter(
        (key: string) => !isNil(variables[key]) && trim(variables[key]) !== ''
      ),
      keys
    )(variables)
    setQuery(_variables)
    refetch(_variables)
  }

  return {
    ...rest,
    refetch: _refetch,
    data,
  }
}
