import { Collapse } from 'antd'
import styled from 'styled-components'

const borderRadius = '8px'

const StyledCollapse = styled(Collapse)`
  &.ant-collapse {
    border-radius: ${borderRadius};
    border-bottom: none;

    > .ant-collapse-item > .ant-collapse-header {
      padding-left: 0;
      background: ${p => p.theme.primary};
      color: #fff;
    }

    &.header-pl-clear > .ant-collapse-item > .ant-collapse-header {
      padding-left: 16px;
    }

    > .ant-collapse-item:first-child > .ant-collapse-header {
      border-top-left-radius: ${borderRadius};
      border-top-right-radius: ${borderRadius};
    }

    > .ant-collapse-item:last-child {
      border-bottom: none;
    }

    > .ant-collapse-item:last-child > .ant-collapse-content {
      border-bottom-left-radius: ${borderRadius};
      border-bottom-right-radius: ${borderRadius};
    }

    > .ant-collapse-item.ant-collapse-item-active:last-child {
      border-bottom: 1px solid #d9d9d9;
    }

    > .ant-collapse-item:not(.ant-collapse-item-active):last-child
      > .ant-collapse-header {
      border-bottom-left-radius: ${borderRadius};
      border-bottom-right-radius: ${borderRadius};
    }
  }
`

export default StyledCollapse
