import { map, mergeAll, pathOr, path, keys } from 'ramda'
import {
  CalendarOutlined,
  ClockCircleOutlined,
  OrderedListOutlined,
  UnorderedListOutlined,
  FontSizeOutlined,
  DatabaseOutlined,
  NumberOutlined,
  BoldOutlined,
  FontColorsOutlined,
} from '@ant-design/icons'
import { uuid } from '@/utils/webHelper'
import TemplateInputText from '@/components/snippet-template-input-text'
import TemplateInputNumber from '@/components/snippet-template-input-number'
import TemplateMultipleSelection from '@/components/snippet-template-multiple-selection'
import TemplateSingleSelection from '@/components/snippet-template-single-selection'
import TemplateDate from '@/components/snippet-template-date'
import TemplateTime from '@/components/snippet-template-time'
import TemplateSelect from '@/components/snippet-template-select'
import TempldateSectionHeader from '@/components/snippet-template-section-header'
import * as Cloze from '@/components/snippet-template-cloze'
import { SnippetProps, ReportTemplate, PhraseTemplate } from '@/constants/types'
const { compose } = require('ramda')

interface SnippetTemplateListItem {
  id: React.Key
  type: string
  name: string
  Component: React.Component
  inline: boolean
}

const snippetTemplateList = [
  {
    id: uuid(),
    type: 'TemplateSectionHeader',
    name: 'Section Header',
    inline: false,
    icon: BoldOutlined,
    Component: TempldateSectionHeader,
  },
  {
    id: uuid(),
    type: 'TemplateInputText',
    name: 'Text',
    inline: false,
    icon: FontSizeOutlined,
    Component: TemplateInputText,
  },
  {
    id: uuid(),
    type: 'TemplateInputNumber',
    name: 'Number',
    inline: false,
    icon: NumberOutlined,
    Component: TemplateInputNumber,
  },
  {
    id: uuid(),
    type: 'TemplateSelect',
    name: 'Drop-down List',
    inline: false,
    icon: DatabaseOutlined,
    Component: TemplateSelect,
  },
  {
    id: uuid(),
    type: 'TemplateMultipleSelection',
    name: 'Multiple Selection List',
    inline: false,
    icon: UnorderedListOutlined,
    Component: TemplateMultipleSelection,
  },
  {
    id: uuid(),
    type: 'TemplateSingleSelection',
    name: 'Single Selection List',
    inline: false,
    icon: OrderedListOutlined,
    Component: TemplateSingleSelection,
  },
  {
    id: uuid(),
    type: 'TemplateDate',
    name: 'Date',
    inline: false,
    icon: CalendarOutlined,
    Component: TemplateDate,
  },
  {
    id: uuid(),
    type: 'TemplateTime',
    name: 'Time',
    inline: false,
    icon: ClockCircleOutlined,
    Component: TemplateTime,
  },
  {
    id: uuid(),
    type: 'TemplateCloze',
    name: 'Cloze',
    inline: true,
    icon: FontColorsOutlined,
    html: Cloze.html,
    Component: Cloze.Component,
  },
]

export const snippetTemplateById = compose(
  mergeAll,
  map((x: SnippetTemplateListItem) => ({ [x.type]: x }))
)(snippetTemplateList)

export const setComponentByTab = compose(
  map((x: ReportTemplate) => ({
    ...x,
    setting: compose(
      mergeAll,
      map((key: string) => ({
        [key]: compose(
          map((s: SnippetProps) => ({
            ...s,
            Component: path([s.type, 'Component'])(snippetTemplateById),
          })),
          pathOr([], ['setting', key])
        )(x),
      })),
      keys,
      pathOr({}, ['setting'])
    )(x),
  })),
  pathOr([], [])
)

export const setComponent = compose(
  map((x: PhraseTemplate) => ({
    ...x,
    setting: compose(
      map((s: SnippetProps) => ({
        ...s,
        Component: path([s.type, 'Component'])(snippetTemplateById),
      })),
      pathOr([], ['setting'])
    )(x),
  })),
  pathOr([], [])
)

export default snippetTemplateList
